<template>
  <div class="inspection-photos">
    <h2 class="sub-heading">检查照片</h2>
    <div class="inspection-main">
      <FileUploadMore :title="title" :is-riequired="isRiequired" :file-limit='fileLimit' list-type="picture-card" :action-url="actionUrl" :file-list-prop="fileList" :tip-text="tipText" :handle-avatar-success="handleAvatarSuccess" @fileRemove="fileRemove"></FileUploadMore>
      <ControlButton type="SaveToback" :next-path="nextPath" :is-disabled='fileList.length === 3 ? false : true' :save-toback="SaveToback" :save-submit="SaveSubmit"></ControlButton>
    </div>
  </div>
</template>

<script>
import FileUploadMore from '@/components/FileUpload/FileUploadMore'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  components: {
    FileUploadMore,
    ControlButton
  },
  name: 'InspectionPhotos',
  data () {
    return {
      inspectionListData: JSON.parse(localStorage.getItem('inspectionListData')),
      title: '办公现场照片',
      isRiequired: true,
      actionUrl: this.$store.state.complianceList.fileUploadUrl,
      tipText: '需上传三张照片，分别为前台、办公场所、和悬挂的营业执照,图片建议尺寸:128X128像素,大小不超过2M,支持JPG、PNG格式',
      nextPath: 'InspectionList',
      previewPath: 'InspectionListPreview',
      fileListObj: [],
      fileLimit: 3
    }
  },
  computed: {
    fileList () {
      // return this.inspectionListData.worksiteImage ? [{ name: '办公现场照片', url: this.inspectionListData.worksiteImageUrl }] : []
      var obj = []
      if (this.fileListObj.length > 0) {
        obj = this.fileListObj
      } else {
        for (var i in this.inspectionListData.fileListObj) {
          obj.push({name: this.inspectionListData.fileListObj[i] ? this.inspectionListData.fileListObj[i].name : '',
            url: this.inspectionListData.fileListObj[i] ? this.inspectionListData.fileListObj[i].url : ''})
        }
      }
      return obj
    }
  },
  methods: {
    handleAvatarSuccess (res) {
      this.inspectionListData.worksiteImage += res.fname + ';'
      this.inspectionListData.worksiteImageUrl += res.furl + ';'
      this.fileListObj = this.fileList.length > 0 ? this.fileList : []
      this.fileListObj.push({name: res.fname, url: res.furl})
      this.inspectionListData.fileListObj = this.fileListObj
      // console.log('fileListObj:', this.fileListObj)
      localStorage.setItem('inspectionListData', JSON.stringify(this.inspectionListData))
    },
    fileRemove (file, fileLists) {
      for (let i in this.inspectionListData.fileListObj) {
        if (file.response) {
          if (this.inspectionListData.fileListObj[i].name === file.response.fname) {
            this.inspectionListData.fileListObj.splice(i, 1)
          }
        } else if (this.inspectionListData.fileListObj[i].name === file.name) {
          this.inspectionListData.fileListObj.splice(i, 1)
        }
      }
      var tempArr = ''
      var tempArrUrl = ''
      for (let y in this.inspectionListData.fileListObj) {
        tempArr += this.inspectionListData.fileListObj[y].name + ';'
        tempArrUrl += this.inspectionListData.fileListObj[y].url + ';'
      }
      this.inspectionListData.worksiteImage = tempArr
      this.inspectionListData.worksiteImageUrl = tempArrUrl
      localStorage.setItem('inspectionListData', JSON.stringify(this.inspectionListData))
    },
    SaveToback () {
      localStorage.setItem('inspectionListData', JSON.stringify(this.inspectionListData))
      this.$message.closeAll()
      this.$message({
        message: '草稿保存成功',
        type: 'success',
        // duration: 0,
        center: true
      })
      this.$router.push(this.nextPath)
    },
    SaveSubmit () {
      localStorage.setItem('inspectionListData', JSON.stringify(this.inspectionListData))
      this.$router.push(this.previewPath)
    }
  }
}
</script>

<style scoped>
.inspection-photos {
}
.inspection-photos .inspection-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.inspection-photos .inspection-main .file-upload {
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
}
.inspection-photos .inspection-main .file-upload strong {
  width: 70px;
}

/* .el-upload-list--picture-card .el-upload-list__item.is-success { */
/* .file-upload .el-upload-list--picture-card .el-upload-list__item,
.file-upload .el-upload--picture-card {
  width: 128px;
  height: 128px;
  line-height: 128px;
} */
</style>



