import axios from 'axios'

// import { Loading } from '@tencent/ekyrin-ui'
// import '@tencent/ekyrin-ui/lib/theme/theme-chalk/index.css'
export default () => {
  axios.defaults.timeout = 10000
  axios.defaults.withCredentials = true
  // let loadingInstance
  // 公共api路径配置
  //  axios.defaults.baseURL = 'https://pay.weixin.qq.com/index.php/xphp/v/cnew_oversea_violated_mch_handle'
  // Axios的拦截器配置
  axios.interceptors.request.use((options) => {
    // loadingInstance = Loading.service({ //加载loading
    //     fullscreen: true,
    //     text: 'Loading',
    //     spinner: 'el-icon-loading',
    //     background: 'rgba(0, 0, 0, 0.7)'
    // })
    // console.log(options)
    return options
  }, error => {
    console.log(error)
  })
  axios.interceptors.response.use(res => {
    // 这里请求成功的一些操作
    // console.log(res)
    return res
    
  }, error => {
    console.log(error)
    if (error.code === 'ECONNABORTED' || error.message === 'Network Error' || error.message.includes('timeout')) {
      // 这里是网络异常的一些操作
      Promise.reject(error)
    }
  })
  return axios
}



