<template>
  <div class="qualification-info">
    <h2 class="sub-heading">资质信息</h2>
    <div class="qualification-main">
      <div class="qualification-item">
        <FileUploadMore :title="title" :class="{disabled:false}" :file-limit='fileLimit' :is-riequired="isRiequired" tip-text="请上传法定代表人（负责人）证件影印件（身份证需上传正反面）" :name="name" list-type="picture-card" :action-url="actionUrl" :file-list-prop="legalPerson" :handle-avatar-success="legalSuccess"  @fileRemove = "(file, fileLists)=> fileRemove(file, fileLists,1)"></FileUploadMore>
      </div>
      <div class="qualification-item" style="padding: 24px 0 16px 0; margin-bottom: 40px;">
        <FileUploadMore :title="institutitle" :is-riequired="isRiequired" :file-limit="fileLimitOne"  tip-text="请上传机构证件的影印件" :name="instituname" list-type="picture-card" :action-url="actionUrl" :file-list-prop="mechanism" :handle-avatar-success="mechanismSuccess"  @fileRemove = "(file, fileLists)=> fileRemove(file, fileLists,2)"></FileUploadMore>
      </div>
      <ControlButton :next-path="nextPath" :is-disabled="!qualificationStatus" :save="Save"></ControlButton>
    </div>
  </div>
</template>

<script>
import FileUploadMore from '@/components/FileUpload/FileUploadMore'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  components: {
    FileUploadMore,
    ControlButton
  },
  data () {
    return {
      fileLimit: 2,
      fileLimitOne: 1,
      title: '法定代表人（负责人）证件影印件',
      isRiequired: true,
      name: '请上传法定代表人（负责人）证件影印件（身份证需上传正反面）',
      institutitle: '机构证件影印件',
      instituname: 'institutionCertificateImage',
      actionUrl: this.$store.state.complianceList.fileUploadUrl,
      nextPath: 'EnterpriseInfo',
      inspectionListData: JSON.parse(localStorage.getItem('inspectionListData')),
      legalFileListObj: [],
      mechanismObj: [],
      qualificationStatus: false
    }
  },
  computed: {
    legalPerson: function () {
      var obj = []
      // var fileList =this.inspectionListData.legalImage ? [{ name: '法定代表人证件影印件', url: this.inspectionListData.legalImageUrl }] : []
      if (this.legalFileListObj.length > 0) {
        obj = this.legalFileListObj
      } else {
        for (var i in this.inspectionListData.legalFileListObj) {
          obj.push({name: this.inspectionListData.legalFileListObj[i] ? this.inspectionListData.legalFileListObj[i].name : '',
            url: this.inspectionListData.legalFileListObj[i] ? this.inspectionListData.legalFileListObj[i].url : ''})
        }
      }
      return obj
    },
    mechanism: function () {
      var mechanismObj = []
      // var mechanismObj = this.inspectionListData.institutionCertificateImage ? [{ name: '机构证件影印件', url: this.inspectionListData.institutionCertificateImageUrl }] : []
      if (this.mechanismObj.length > 0) {
        mechanismObj = this.mechanismObj
      } else {
        for (var i in this.inspectionListData.mechanismObj) {
          mechanismObj.push({name: this.inspectionListData.mechanismObj[i].name ? this.inspectionListData.mechanismObj[i].name : '',
            url: this.inspectionListData.mechanismObj[i].url ? this.inspectionListData.mechanismObj[i].url : ''})
        }
      }
      return mechanismObj
    }
  },
  created () {
    if (this.inspectionListData.legalFileListObj !== undefined && this.inspectionListData.mechanismObj !== undefined) {
      this.qualificationStatus = (Boolean(this.inspectionListData.legalFileListObj.length) && Boolean(this.inspectionListData.mechanismObj.length))
    }
  },
  methods: {
    legalSuccess (res) {
      this.inspectionListData.legalImage += res.fname + ';'
      this.inspectionListData.legalImageUrl = res.furl
      this.legalFileListObj = this.legalPerson.length > 0 ? this.legalPerson : []
      this.legalFileListObj.push({name: res.fname, url: res.furl})
      this.inspectionListData.legalFileListObj = this.legalFileListObj
      localStorage.setItem('inspectionListData', JSON.stringify(this.inspectionListData))
      if (this.inspectionListData.legalFileListObj !== undefined && this.inspectionListData.mechanismObj !== undefined) {
        this.qualificationStatus = (Boolean(this.inspectionListData.legalFileListObj.length) && Boolean(this.inspectionListData.mechanismObj.length))
      }
    },
    mechanismSuccess (res) {
      this.inspectionListData.institutionCertificateImage += res.fname + ';'
      this.inspectionListData.institutionCertificateImageUrl = res.furl
      this.mechanismObj = this.mechanism.length > 0 ? this.mechanism : []
      this.mechanismObj.push({name: res.fname, url: res.furl})
      this.inspectionListData.mechanismObj = this.mechanismObj
      localStorage.setItem('inspectionListData', JSON.stringify(this.inspectionListData))
      if (this.inspectionListData.legalFileListObj !== undefined && this.inspectionListData.mechanismObj !== undefined) {
        this.qualificationStatus = (Boolean(this.inspectionListData.legalFileListObj.length) && Boolean(this.inspectionListData.mechanismObj.length))
      }
    },
    fileRemove (file, fileLists, num) {
      switch (parseInt(num)) {
        case 1:
          for (let i in this.inspectionListData.legalFileListObj) {
            if (file.response) {
              if (this.inspectionListData.legalFileListObj[i].name === file.response.fname) {
                this.inspectionListData.legalFileListObj.splice(i, 1)
              }
            } else if (this.inspectionListData.legalFileListObj[i].name === file.name) {
              this.inspectionListData.legalFileListObj.splice(i, 1)
            }
          }
          var tempArr = ''
          for (let y in this.inspectionListData.legalFileListObj) {
            tempArr += this.inspectionListData.legalFileListObj[y].name + ';'
          }
          this.inspectionListData.legalImage = tempArr
          break
        case 2:
          for (var index in this.inspectionListData.mechanismObj) {
            if (file.response) {
              if (this.inspectionListData.mechanismObj[index].name === file.response.fname) {
                this.inspectionListData.mechanismObj.splice(index, 1)
              }
            } else if (this.inspectionListData.mechanismObj[index].name === file.name) {
              this.inspectionListData.mechanismObj.splice(index, 1)
            }
          }
          var inspeArr = ''
          for (let m in this.inspectionListData.mechanismObj) {
            inspeArr += this.inspectionListData.mechanismObj[m].name + ';'
          }
          this.inspectionListData.institutionCertificateImage = inspeArr
          break
      }
      localStorage.setItem('inspectionListData', JSON.stringify(this.inspectionListData))
      if (this.inspectionListData.legalFileListObj !== undefined && this.inspectionListData.mechanismObj !== undefined) {
        this.qualificationStatus = (Boolean(this.inspectionListData.legalFileListObj.length) && Boolean(this.inspectionListData.mechanismObj.length))
      }
    },
    Save () {
      localStorage.setItem('inspectionListData', JSON.stringify(this.inspectionListData))
      this.$message.closeAll()
      this.$message({
        message: '草稿保存成功',
        type: 'success',
        // duration: 0,
        center: true
      })
      // return false
    }
  }
}
</script>

<style scoped>
.qualification-info {
  /* width: 1200px; */
  width: 100%;
}
.qualification-info .qualification-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.qualification-info .qualification-main {
}
.qualification-info .qualification-main .qualification-item {
  line-height: 20px;
  display: flex;
  box-sizing: border-box;
  border-bottom: 1px solid #ddd;
}
.btnReverse{
    margin-left: -79px;
  }
</style>

<style>
</style>



