<template>
  <div class="survey-rating">
    <h2 class="sub-heading">尽职调查/评级</h2>
    <div class="survey-rating-main">
      <h3 class="sub-heading">必填信息</h3>
      <div class="required-list">
        <StepBar :stepArr="stepArr" :FillIn="FillIn"></StepBar>
      </div>
      <h3 class="sub-heading">其他信息</h3>
      <div class="additiaonal-info-item" :class="additiaonalInfo.status">
        <div class="step-text">
          <h4>{{additiaonalInfo.title}}</h4>
          <p>{{additiaonalInfo.text}}</p>
        </div>
        <div class="step-operat" @click="FillIn(additiaonalInfo.path)">{{additiaonalInfo.operat}}</div>
      </div>
      <ControlButton type="ToBack" :is-disabled="!surveyRatingStatus" :is-loading="isLoading" :save-submit="SaveSubmit"></ControlButton>
    </div>
  </div>
</template>

<script>
import StepBar from '@/components/StepBar/StepBar'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  components: {
    StepBar,
    ControlButton
  },
  data () {
    return {
      fla: 0,
      getSurveyRatingUrl: this.$store.state.complianceList.getSurveyRatingUrl,
      saveSurveyRatingUrl: this.$store.state.complianceList.saveSurveyRatingUrl,
      ecc_csrf_token: this.$store.state.complianceList.ecc_csrf_token,
      surveyRatingData: {},
      isLoading: false
      // isDisabled: true
    }
  },
  mounted () {
    var a = localStorage.getItem('internalStsa')
    if (a === '1') {
      this.fla = 1
    }
  },
  computed: {
    recordId () {
      return localStorage.getItem('previewRecordId')
    },
    operatingConditionStatus () {
      let status = Boolean(this.surveyRatingData.legalImage) && Boolean(this.surveyRatingData.legalImage)
      let isRiequiredData = ['companyName', 'companyType', 'companyCode', 'businessLicensePlace', 'legalName', 'legalCertificateCode', 'contactName', 'contactTel']
      for (let i in isRiequiredData) {
        if (!this.surveyRatingData[isRiequiredData[i]]) status = false
      }
      return status
    },
    managementTeamStatus () {
      let tempStatId = localStorage.getItem('statusId')
      let stas = localStorage.getItem('manageStsa' + tempStatId)
      if (stas !== '1') {
        return false
      }
      return true
      // return Boolean(this.surveyRatingData.checkAgentDutyTeam)
    },
    financialStatus () {
      return Boolean(this.surveyRatingData.isRegisteredCapitalCoin && this.surveyRatingData.isStatementAudit && this.surveyRatingData.financialStatement)
    },
    internalControlStatus () {
      let status = true
      let tempStatId = localStorage.getItem('statusId')
      let stas = localStorage.getItem('internalStsa' + tempStatId)
      if (stas !== '1') {
        return false
      }
      let isRiequiredData = ['isOrganizationSound', 'isInternalControlSound', 'isAntiMoneyLaundering', 'isRiskManagementSound', 'isSecuritySound', 'isEmergencySound', 'isSupportSound']
      for (let i in isRiequiredData) {
        if (!this.surveyRatingData[isRiequiredData[i]]) status = false
      }
      return status
    },
    creditStatusStatus () {
      let status = true
      let tempStatId = localStorage.getItem('statusId')
      let stas = localStorage.getItem('credisStsa' + tempStatId)
      let isRiequiredData = ['litigationNumber', 'litigationMoney', 'undoneLitigationNumber', 'undoneLitigationMoney']
      if (this.surveyRatingData.isMajorLitigation === '1') {
        for (let i in isRiequiredData) {
          if (!this.surveyRatingData[isRiequiredData[i]] || stas !== '1') status = false
        }
      } else {
        if (stas !== '1') {
          return false
        }
      }
      return status
    },
    professionalCompetenceStatus () {
      let status = true
      let isRiequiredData = ['contractStartTime', 'cooperationScope', 'cooperationPlace', 'serviceStaffNumber', 'merchantNumber', 'machineNumber', 'companyWorkYear', 'seniorManagerNumber']
      for (let i in isRiequiredData) {
        if (!this.surveyRatingData[isRiequiredData[i]]) status = false
      }
      return status
    },
    businessComplianceStatus () {
      let status = true
      let tempStatId = localStorage.getItem('statusId')
      let stas = localStorage.getItem('business' + tempStatId)
      if (stas !== '1') {
        return false
      }
      let isRiequiredData = ['conformanceStaffNumber', 'isConformanceDepartment', 'isConformanceRegulation', 'isConformanceProcess']
      for (let i in isRiequiredData) {
        if (!this.surveyRatingData[isRiequiredData[i]] || this.surveyRatingData.conformanceStaffNumber === '') status = false
      }
      return status
    },
    personalInfoProtectionStatus () {
      let status = true
      let tempStatId = localStorage.getItem('statusId')
      let stas = localStorage.getItem('personalStsa' + tempStatId)
      if (stas !== '1') {
        return false
      }
      let isRiequiredData = ['isSecurityAttestation', 'isInformationManagement', 'isInformationProcess', 'isStaffMaintainSecrecy', 'isHierachicalAuthorize', 'isInfomationEncryption', 'isSecrecyAudit', 'isSecrecyDangerCheck']
      for (let i in isRiequiredData) {
        if (!this.surveyRatingData[isRiequiredData[i]]) status = false
      }
      return status
    },
    riskControlAbilityStatus () {
      let status = true
      let tempStatId = localStorage.getItem('statusId')
      let stas = localStorage.getItem('RiskControlStsa' + tempStatId)
      let isRiequiredData = ['riskHappenedNumber', 'isMchRiskHappened', 'swindleMoney', 'swindleRatio']
      if (this.surveyRatingData.isRiskHappened === '1') {
        for (let i in isRiequiredData) {
          if (!this.surveyRatingData[isRiequiredData[i]] || stas !== '1') status = false
        }
      } else {
        if (stas !== '1') {
          return false
        }
      }
      return status
    },
    stepArr () {
      return [{
        title: '经营状况',
        text: '请填写公司经营状况相关信息',
        status: this.operatingConditionStatus ? 'complete' : '',
        operat: this.operatingConditionStatus ? '已完成' : '去填写',
        path: 'OperatingConditions'
      }, {
        title: '管理团队',
        text: '请填写公司管理团队相关信息',
        status: this.managementTeamStatus ? 'complete' : '',
        operat: this.managementTeamStatus ? '已完成' : '去填写',
        path: 'ManagementTeam'
      }, {
        title: '财务状况',
        text: '请填写公司财务状况相关信息',
        status: this.financialStatus ? 'complete' : '',
        operat: this.financialStatus ? '已完成' : '去填写',
        path: 'FinancialStatus'
      }, {
        title: '内控水平',
        text: '请填写公司内控水平相关信息',
        status: this.internalControlStatus ? 'complete' : '',
        operat: this.internalControlStatus ? '已完成' : '去填写',
        path: 'InternalControl'
      }, {
        title: '信用状况',
        text: '请填写公司信用状况相关信息',
        status: this.creditStatusStatus ? 'complete' : '',
        operat: this.creditStatusStatus ? '已完成' : '去填写',
        path: 'CreditStatus'
      }, {
        title: '专业能力',
        text: '请填写公司专业能力相关信息',
        status: this.professionalCompetenceStatus ? 'complete' : '',
        operat: this.professionalCompetenceStatus ? '已完成' : '去填写',
        path: 'ProfessionalCompetence'
      }, {
        title: '业务合规',
        text: '请填写公司业务合规相关信息',
        status: this.businessComplianceStatus ? 'complete' : '',
        operat: this.businessComplianceStatus ? '已完成' : '去填写',
        path: 'BusinessCompliance'
      }, {
        title: '个人信息保护',
        text: '请填写公司个人信息保护相关信息',
        status: this.personalInfoProtectionStatus ? 'complete' : '',
        operat: this.personalInfoProtectionStatus ? '已完成' : '去填写',
        path: 'PersonalInfoProtection'
      }, {
        title: '风控能力',
        text: '请填写公司风控能力相关信息',
        status: this.riskControlAbilityStatus ? 'complete' : '',
        operat: this.riskControlAbilityStatus ? '已完成' : '去填写',
        path: 'RiskControlAbility'
      }]
    },
    surveyRatingStatus () {
      let status = true
      // if(this.surveyRatingData.cooperationScope=='11' && this.surveyRatingData.otherBusiness==''){
      //   status = false;
      // }
      // let status = true
      for (let i in this.stepArr) {
        if (this.stepArr[i].status !== 'complete') status = false
      }
      return status
    },
    additiaonalInfo () {
      return {
        title: '补充信息',
        text: '根据实际审核情况,额外要求商家提供指定的补充资料',
        status: this.surveyRatingData.agentSuggestion ? 'complete' : '',
        operat: this.surveyRatingData.agentSuggestion ? '已完成' : '去填写',
        path: 'OtherComments'
      }
    }
  },
  // watch: {
  //   stepArr: {
  //     handler () {
  //      let status = true
  //       for (let i = 0; i < this.stepArr.length; i++) {
  //         if (this.stepArr[i].status !== 'complete') {
  //           status= false
  //           return
  //         }
  //       }
  //       console.log('isDisabled:',status)
      
  //     },
  //     // immediate: true,
  //     // deep: true
    
  //   }
  // },
  created () {
    let obj = JSON.parse(localStorage.getItem('surveyRatingData')) || {}
    if (obj.recordId !== this.recordId) {
      let params = { recordId: this.recordId }
      this.$axios.get(this.getSurveyRatingUrl, { params: params }).then(res => {
        console.log('请求成功', res)
        if (res && res.data && res.data.return_code === '0') {
          let list = res.data.list
          list.recordId = this.recordId
          list.statusId = localStorage.getItem('statusId')
            console.log('list:',list)
          //数据请求回来对一些默认值进行处理
          list.institutionNumber = '';
          list.isConformanceText = '';
          list.isRegisteredCapitalCoin = '0';
          list.lastYearCashRatio = '';
          list.lastYearDebtRatio = '';
          list.lastYearCapitalRatio = '';
          list.lastYearAccountRatio = '';
          list.lastYearGainRatio = '';
          list.capitalDebtRatio = '';
          this.surveyRatingData = list
       
                           
          localStorage.setItem('surveyRatingData', JSON.stringify(list))
          } else {
          this.$message.closeAll()
          this.$message({
            message: res.data.return_msg ? res.data.return_msg : '数据返回错误',
            type: 'error',
            // duration: 0,
            center: true
          })
        }
      }).catch(res => {
        // console.log('请求出错', res)
        this.$message.closeAll()
        this.$message({
          message: '请求超时,请刷新页面重试',
          type: 'error',
          // duration: 0,
          center: true
        })
      }) // .finally(() => { this.tableLoading = false })
    } else {
      this.surveyRatingData = obj
    }
    // console.log('recordId:', this.recordId, this.surveyRatingData)
  },
  methods: {
    FillIn (path) {
      this.$router.push(path)
    },
    SaveSubmit () {
      if(this.surveyRatingData.cooperationScope=='11' && this.surveyRatingData.otherBusiness==''){
       this.$message.error('请填写专业能力的其他业务范围');
       return;
      }
      this.isLoading = true
      let params = new FormData() // 创建form对象
      for (let i in this.surveyRatingData) {
        params.append(i, this.surveyRatingData[i])
      }
      params.append('ecc_csrf_token', this.ecc_csrf_token)
      // console.log(this.surveyRatingData, params)
      let config = { headers: { 'Content-Type': 'multipart/form-data' } }
      this.$axios.post(this.saveSurveyRatingUrl, params, config).then(res => {
        // console.log('请求成功', res)
        this.isLoading = false
        if (res && res.data && res.data.return_code === '0') {
          sessionStorage.setItem('submitStatus', true)
          this.$router.push('ResultSubmitted')
        } else {
          sessionStorage.setItem('submitStatus', false)
          this.$message.closeAll()
          this.$message({
            // message: res.data.return_msg,
            message: '登录超时，请刷新页面重新登录',
            type: 'error',
            // duration: 0,
            center: true
          })
        }
      }).catch(res => {
        // console.log('请求出错', res)
        this.isLoading = false
        sessionStorage.setItem('submitStatus', false)
        this.$message.closeAll()
        this.$message({
          message: '请求超时,请刷新页面重试',
          type: 'error',
          // duration: 0,
          center: true
        })
      }) // .finally(() => { this.tableLoading = false })
      //上报
      var WXPAY_JSLOG_REPORT = null;
      if(WXPAY_JSLOG_REPORT) WXPAY_JSLOG_REPORT.send({common_log:"SurveyRatingSubmit"}); // 提交
      
    }
  }
}
</script>

<style scoped>
.survey-rating {
  /* width: 1200px; */
  width: 100%;
}
.survey-rating .survey-rating-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
  margin-bottom: 100px;
}
.survey-rating .survey-rating-main .required-list {
  margin-bottom: 60px;
}
/* 其他信息 */
.survey-rating .additiaonal-info-item {
  width: calc(100% - 40px);
  height: 80px;
  display: flex;
  box-sizing: border-box;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  margin: 0 0 60px 40px;
}
.survey-rating .additiaonal-info-item .step-text {
  flex: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.survey-rating .additiaonal-info-item .step-text h4 {
  font-weight: bold;
  color: #333;
}
.survey-rating .additiaonal-info-item .step-text p {
  color: #999;
}
.survey-rating .additiaonal-info-item .step-operat {
  display: flex;
  align-items: center;
  color: cornflowerblue;
  cursor: pointer;
}
.survey-rating .additiaonal-info-item.complete .step-operat {
  color: limegreen;
}
.survey-rating .additiaonal-info-item .step-operat:after {
  content: '';
  display: block;
  width: 8px;
  height: 8px;
  border-top: 1px solid #aaa;
  border-right: 1px solid #aaa;
  transform: rotate(45deg);
  margin: 0 30px 0 15px;
}
</style>



