<template>
  <div class="financial-status">
    <h2 class="sub-heading">财务状况</h2>
    <div class="financial-status-main">
      <InputComponent :form-data="radioData"></InputComponent>
      <div class="financial-statements">
        <FileUploadMore :title="financialStatement.title" :is-riequired="financialStatement.isRiequired" :file-limit='3' :tip-text="financialStatement.tipText" :file-list-prop="financialList" :handle-avatar-success="financialSuccess" @fileRemove = "(file, fileLists)=> financialRemove(file, fileLists)"></FileUploadMore>

      </div>
      <div class="evidence">
        <FileUploadMore :title="evidence.title" :tip-text="evidence.tipText" :file-limit='3' :file-list-prop="evidenceList" :handle-avatar-success="evidenceSuccess" @fileRemove= "(file, fileLists)=> evidenceRemove(file, fileLists)"></FileUploadMore>
        <!-- <InputComponent :form-data="evidenceData"></InputComponent> -->
        <el-form :model="evidenceForm" :rules="formRules" ref="evidenceForm" label-width="100px" label-position="left" class="evidence-form">
          <el-form-item v-for="(item, i) in evidenceData" :label="item.title" :prop="item.key" :key="i">
            <el-input v-if="item.valueType == 'number'" type="string" v-model="evidenceForm[item.key]"></el-input>
            <el-input v-else v-model="evidenceForm[item.key]"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div class="financialDetails">
        <!-- <InputComponent :form-data="financialDetails"></InputComponent> -->
        <el-form :model="financialForm" :rules="formRules" ref="financialForm" label-width="100px" label-position="left" class="financial-form">
          <el-form-item v-for="(item, i) in financialDetails" :label="item.title" :prop="item.key" :key="i">
            <el-select v-if="item.valueType == 'select'" v-model="financialForm[item.key]">
              <el-option v-for="(itemj, j) in item.options" :label="itemj.text" :value="itemj.value" :key="j + itemj.text"></el-option>
            </el-select>
            <el-date-picker v-else-if="item.valueType == 'date'" type="date" placeholder="选择日期" v-model="financialForm[item.key]" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            <el-date-picker v-else-if="item.valueType == 'daterange'" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" v-model="financialForm[item.key]" range-separator="-" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            <el-input v-else-if="item.valueType == 'number'" type="number" v-model="financialForm[item.key]"></el-input>
            <el-input v-else v-model="financialForm[item.key]"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <ControlButton :next-path="nextPath" :is-disabled="!FinancialStatus" :save="Save"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import FileUploadMore from '@/components/FileUpload/FileUploadMore'
import ControlButton from '@/components/ControlButton/ControlButton'
import { validatetoFixedNew, isBtnZeroToHundred, isBtnZeroToHundredTwo,validatetoFixedNewTwo} from '@/units/validator'

export default {
  name: 'FinancialStatus',
  components: {
    InputComponent,
    FileUploadMore,
    ControlButton
  },
  data () {
    return {
      surveyRatingData: JSON.parse(localStorage.getItem('surveyRatingData')),
      financialStatementObj: [],
      evidenceObj: [],
      keyStr: {
        // isRegisteredCapitalCoin: '注册资本是否为实缴货币',
        // isStatementAudit: '财务报表是否经审计',
        // financialStatement: '上传财务报表',
        // lastYearAudit: '上一年度审计证明材料',
        // lastYearCashRatio: '上年现金流量比率(%)',
        lastYearDebtRatio: '上年长期资本负债率(%)',
        lastYearCapitalRatio: '上年应收账款周转率(%)',
        lastYearAccountRatio: '上年净资产增长率(%)',
        lastYearGainRatio: '上年经营净利率(%)',
        lastYearIncoming: '上年总收入(万元)',
        lastYearProfit: '上年净利润(万元)',
        capitalDebtRatio: '资产负债率(%)',
        totalCapital: '资产总额(万元)',
        totalTax: '交税总额(万元)'
      },
      radioData: [{
        title: '注册资本是否为实缴货币',
        value: null,
        isRiequired: true,
        valueType: 'radio'
      }, {
        title: '财务报表是否经审计',
        value: null,
        isRiequired: true,
        valueType: 'radio'
      }],
      financialStatement: {
        title: '上传财务报表',
        isRiequired: true,
        fileList: [],
        tipText: '文件大小2M,资产负债表、利润表、现金流量表，最多3个文件'
      },
      evidence: {
        title: '上一年度审计证明材料',
        fileList: [],
        tipText: '上一年度审计证明材料，最多3个文件'
      },
      evidenceData: [{
        title: '上年现金流量比率(%)',
        key: 'lastYearCashRatio',
        value: '',
        isRiequired: false,
        valueType: 'number'
      }],
      evidenceForm: {},
      financialDetails: [],
      financialForm: [],
      formRules: {
        lastYearCashRatio: [
          { validator: isBtnZeroToHundredTwo, trigger: 'blur' }
        ],
        lastYearDebtRatio: [
          { validator: isBtnZeroToHundred, trigger: 'blur' }
        ],
        lastYearCapitalRatio: [
          { validator: isBtnZeroToHundredTwo, trigger: 'blur' }
        ],
        lastYearAccountRatio: [
          { validator: isBtnZeroToHundredTwo, trigger: 'blur' }
        ],
        lastYearGainRatio: [
          { validator: isBtnZeroToHundredTwo, trigger: 'blur' }
        ],
        lastYearIncoming: [
          { validator: validatetoFixedNewTwo, trigger: 'blur' }
        ],
        lastYearProfit: [
          { validator: validatetoFixedNewTwo, trigger: 'blur' }
        ],
        capitalDebtRatio: [
          { validator: isBtnZeroToHundred, trigger: 'blur' }
        ],
        totalCapital: [
          { validator: validatetoFixedNewTwo, trigger: 'blur' }
        ],
        totalTax: [
          { validator: validatetoFixedNew, trigger: 'blur' }
        ]
      },
      nextPath: 'InternalControl'
    }
  },
  computed: {
    FinancialStatus () {
      return Boolean(this.surveyRatingData.financialStatement)
    },
    financialList: function () {
      var obj = []
      if (this.financialStatementObj.length > 0) {
        obj = this.financialStatementObj
      } else {
        for (var i in this.surveyRatingData.financialStatementObj) {
          obj.push({name: this.surveyRatingData.financialStatementObj[i] ? this.surveyRatingData.financialStatementObj[i].name : '',
            url: this.surveyRatingData.financialStatementObj[i] ? this.surveyRatingData.financialStatementObj[i].url : ''})
        }
      }
      return obj
    },
    evidenceList: function () {
      var obj = []
      if (this.evidenceObj.length > 0) {
        obj = this.evidenceObj
      } else {
        for (var i in this.surveyRatingData.evidenceObj) {
          obj.push({name: this.surveyRatingData.evidenceObj[i] ? this.surveyRatingData.evidenceObj[i].name : '',
            url: this.surveyRatingData.evidenceObj[i] ? this.surveyRatingData.evidenceObj[i].url : ''})
        }
      }
      return obj
    }
  },
  watch: {
    radioData: {
      handler (val, oldval) {
        console.log(val,oldval)
        this.surveyRatingData.isRegisteredCapitalCoin = val[0].value
        this.surveyRatingData.isStatementAudit = val[1].value
      },
      immediate: false,
      deep: true
    },
    evidenceForm: {
      handler (val, oldval) {
        this.Update(val)
      },
      immediate: false,
      deep: true
    },
    financialForm: {
      handler (val, oldval) {
        this.Update(val)
      },
      immediate: false,
      deep: true
    }
  },
  created () {
    // console.log(this.surveyRatingData);
 
    this.radioData[0].value = this.surveyRatingData.isRegisteredCapitalCoin
       
    this.radioData[1].value = this.surveyRatingData.isStatementAudit
    // this.evidenceData[0].value = this.surveyRatingData.lastYearCashRatio === '0' ? '' : this.surveyRatingData.lastYearCashRatio
    this.evidenceData[0].value = this.surveyRatingData.lastYearCashRatio
    
    this.evidence.fileList = this.surveyRatingData.lastYearAudit ? [{ name: this.surveyRatingData.lastYearAudit, url: this.surveyRatingData.lastYearAuditUrl }] : []
    for (let i in this.keyStr) {
      // let obj = { title: this.keyStr[i], key: i, value: this.surveyRatingData[i] === '0' ? '' : this.surveyRatingData[i], isRiequired: false, valueType: '' }
      let obj = { title: this.keyStr[i], key: i, value:this.surveyRatingData[i], isRiequired: false, valueType: '' }
      this.financialDetails.push(obj)
    }
    let evidenceForm = {}
    let financialForm = {}
    
    for (let i in this.evidenceData) {
      evidenceForm[this.evidenceData[i].key] = this.evidenceData[i].value
    }
    
    for (let i in this.financialDetails) {
      financialForm[this.financialDetails[i].key] = this.financialDetails[i].value
    }
    this.evidenceForm = evidenceForm
    this.financialForm = financialForm
    // console.log(this.evidenceForm, this.financialForm)
  },
  methods: {
    valueText (item, value) {
      let text = Array.isArray(value) ? value[0] + ' - ' + value[1] : item.valueType === 'select' ? item.valText[value] : item.valueType === 'radio' ? (value === '1' ? '是' : '否') : value
      return text
    },
    financialSuccess (res) {
      this.surveyRatingData.financialStatement += res.fname + ';'
      this.surveyRatingData.financialStatementUrl = res.furl
      this.financialStatementObj = this.financialList.length > 0 ? this.financialList : []
      this.financialStatementObj.push({name: res.fname, url: res.furl})
      this.surveyRatingData.financialStatementObj = this.financialStatementObj
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
    },
    financialRemove (file, fileLists) {
      for (let i in this.surveyRatingData.financialStatementObj) {
        if (file.response) {
          if (this.surveyRatingData.financialStatementObj[i].name === file.response.fname) {
            this.surveyRatingData.financialStatementObj.splice(i, 1)
          }
        } else if (this.surveyRatingData.financialStatementObj[i].name === file.name) {
          this.surveyRatingData.financialStatementObj.splice(i, 1)
        }
      }
      var tempArr = ''
      for (let y in this.surveyRatingData.financialStatementObj) {
        tempArr += this.surveyRatingData.financialStatementObj[y].name + ';'
      }
      this.surveyRatingData.financialStatement = tempArr
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
    },
    evidenceSuccess (res) {
      this.surveyRatingData.lastYearAudit += res.fname + ';'
      this.surveyRatingData.lastYearAuditUrl = res.furl
      this.evidenceObj = this.evidenceList.length > 0 ? this.evidenceList : []
      this.evidenceObj.push({name: res.fname, url: res.furl})
      this.surveyRatingData.evidenceObj = this.evidenceObj
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
    },
    evidenceRemove (file, fileLists) {
      for (let i in this.surveyRatingData.evidenceObj) {
        if (file.response) {
          if (this.surveyRatingData.evidenceObj[i].name === file.response.fname) {
            this.surveyRatingData.evidenceObj.splice(i, 1)
          }
        } else if (this.surveyRatingData.evidenceObj[i].name === file.name) {
          this.surveyRatingData.evidenceObj.splice(i, 1)
        }
      }
      var tempArr = ''
      for (let y in this.surveyRatingData.evidenceObj) {
        tempArr += this.surveyRatingData.evidenceObj[y].name + ';'
      }
      this.surveyRatingData.lastYearAudit = tempArr
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
    },
    Save () {
      // this.surveyRatingData.isRegisteredCapitalCoin = this.radioData[0].value
      // this.surveyRatingData.isStatementAudit = this.radioData[1].value
      let evidenceStatus = true
      let financialStatus = true
      this.$refs['evidenceForm'].validate((valid) => {
        if (!valid) {
          evidenceStatus = false
          return false
        }
      })
      this.$refs['financialForm'].validate((valid) => {
        if (!valid) {
          financialStatus = false
          return false
        }
      })
      if (evidenceStatus && financialStatus) {
        // let data = this.financialDetails
        // for (let i in data) {
        //   this.surveyRatingData[data[i].key] = data[i].value
        // }
        localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
        this.$message.closeAll()
        this.$message({
          message: '草稿保存成功',
          type: 'success',
          // duration: 0,
          center: true
        })
        return true
      }
      return false
    },
    Update (data) {
      for (let i in data) {
        this.surveyRatingData[i] = data[i]
      }
      // console.log(this.surveyRatingData)
    }
  }
}
</script>

<style scoped>
.financial-status .financial-status-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
  margin-bottom: 100px;
}

.financial-status .financial-status-main .financial-statements {
  border-bottom: 1px solid #ddd;
  margin: 50px 0;
}
.financial-status .financial-status-main .financialDetails {
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
}
.financial-status .financial-status-main .input-component {
  justify-content: space-between;
}
.financial-status .financial-status-main .evidence {
  justify-content: space-between;
  display: flex;
}
</style>

<style>
.financial-status-main .input-component-item strong,
.financial-status-main .file-upload strong {
  width: 93px;
}
.financial-status .financial-status-main .evidence .input-component-item {
  align-items: flex-start;
}
.financial-status-main .financialDetails {
  padding-bottom: 20px;
}
.financial-status-main .input-component-item {
  height: 74px;
}
</style>



