<template>
  <div class="homemade-pagination">
    <el-button type="info" plain @click="Jump(1)" :disabled="current <= 1">首页</el-button>
    <el-button type="info" plain @click="Jump(current - 1)" class="prev" :disabled="current <= 1"></el-button>
    <div class="pager">{{Number(current) + " / " + max}}</div>
    <el-button type="info" plain @click="Jump(current + 1)" class="next" :disabled="current >= max"></el-button>
    <el-button type="info" plain @click="Jump(max)" :disabled="current >= max">尾页</el-button>
    <input type="number" autocomplete="off" min="1" :max="max" class="el-input__inner" v-model="inputVal">
    <el-button type="info" plain @click="Jump(inputVal)">跳转</el-button>
  </div>
</template>

<script>
export default {
  name: 'HomemadePagination',
  props: {
    pageSize: {
      type: Number,
      default: 6
    },
    total: {
      type: Number,
      required: true
    },
    current: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {
      inputVal: 1
    }
  },
  computed: {
    max () { return this.total !== 0 ? Math.ceil(this.total / this.pageSize) : 1 }
  },
  watch: {
    current (val, oldVal) {
      this.inputVal = val
    }
  },
  methods: {
    Jump (cur) {
      cur = Number(cur)
      if (cur === this.current) return
      if (cur < 1) {
        cur = 1
        this.inputVal = 1
      } else if (cur > this.max) {
        cur = this.max
        this.inputVal = this.max
      }
      this.$emit('CurChange', cur)
    }
  }
}
</script>

<style scoped>
.homemade-pagination {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.homemade-pagination .el-input__inner,
.homemade-pagination .el-button {
  width: 60px;
  height: 30px;
  border-radius: 2px;
  color: #555;
  margin: 0 0 0 10px;
  text-align: center;
  line-height: 30px;
}
.homemade-pagination .el-button {
  border: none;
  background: #eee;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.homemade-pagination .el-button.is-disabled {
  color: #bcbec2;
  background-color: #f4f4f5;
  border-color: #e9e9eb;
}
.homemade-pagination .el-button.prev {
  width: 30px;
  padding-left: 6px;
}
.homemade-pagination .el-button.next {
  width: 30px;
  padding-right: 6px;
}
.homemade-pagination .el-button.prev:before,
.homemade-pagination .el-button.next:before {
  content: '';
  display: block;
  width: 10px;
  height: 10px;
  border-top: 1px solid #333;
}
.homemade-pagination .el-button.prev:before {
  border-left: 1px solid #333;
  transform: rotate(-45deg);
}
.homemade-pagination .el-button.next:before {
  border-right: 1px solid #333;
  transform: rotate(45deg);
}
.homemade-pagination .el-button.prev.is-disabled:before,
.homemade-pagination .el-button.next.is-disabled:before {
  border-color: #bcbec2;
}
.homemade-pagination .pager {
  width: 90px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* number input 禁止上下箭头 */
/* 谷歌 */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  margin: 0;
}
/* 火狐 */
.input-component-item input[type='number'] {
  -moz-appearance: textfield;
}
</style>



