<template>
  <div class="personal-info-protection">
    <h2 class="sub-heading">个人信息保护</h2>
    <div class="personal-info-protection-main">
      <InputComponent :form-data="radioData"></InputComponent>
      <ControlButton :next-path="nextPath" :is-disabled="!personalInfoProtectionStatus" :save="Save"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  name: 'PersonalInfoProtection',
  components: {
    InputComponent,
    ControlButton
  },
  data () {
    return {
      surveyRatingData: JSON.parse(localStorage.getItem('surveyRatingData')),
      keyStr: {
        isSecurityAttestation: '是否取得相关安全测评认证',
        isInformationManagement: '是否建立完善的信息管理及保密制度',
        isInformationProcess: '是否建立信息管理流程',
        isStaffMaintainSecrecy: '员工是否签订保密协议',
        isHierachicalAuthorize: '系统管理是否采取分级授权',
        isInfomationEncryption: '信息是否进行加密存储',
        isSecrecyAudit: '是否每年开展信息安全审计',
        isSecrecyDangerCheck: '是否每半年开展信息安全隐患排查'
      },
      radioData: [],
      nextPath: 'RiskControlAbility'
    }
  },
  mounted () {
    var tempStatId = localStorage.getItem('statusId') ? localStorage.getItem('statusId') : '00'
    localStorage.setItem('personalStsa' + tempStatId, '1')
  },
  computed: {
    personalInfoProtectionStatus () {
      let status = true
      for (let i in this.keyStr) {
        if (!this.surveyRatingData[i]) status = false
      }
      return status
    }
  },
  watch: {
    radioData: {
      handler (val, oldval) {
        for (let i in val) {
          this.surveyRatingData[val[i].key] = val[i].value
        }
      },
      immediate: false,
      deep: true
    }
  },
  created () {
    for (let i in this.keyStr) {
      let obj = { title: this.keyStr[i], key: i, value: this.surveyRatingData[i], isRiequired: true, valueType: 'radio' }
      this.radioData.push(obj)
    }
  },
  methods: {
    Save () {
      // let data = this.professionalData
      // for (let i in data) {
      //   this.surveyRatingData[data[i].key] = data[i].value
      // }
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
      this.$message.closeAll()
      this.$message({
        message: '草稿保存成功',
        type: 'success',
        // duration: 0,
        center: true
      })
    }
  }
}
</script>

<style scoped>
.personal-info-protection .personal-info-protection-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.personal-info-protection .personal-info-protection-main .input-component {
  padding-bottom: 30px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
  justify-content: space-between;
}
</style>

<style>
.personal-info-protection-main .input-component .input-component-item {
  min-height: 74px;
}
.personal-info-protection-main .input-component-item strong {
  width: 93px;
}
</style>



