<template>
  <div class="credit-status">
    <h2 class="sub-heading">信用状况</h2>
    <div class="credit-status-main">
      <InputComponent :form-data="radioData"></InputComponent>
      <!-- <InputComponent v-show="radioData[0].value === '1'" :form-data="caseDetails"></InputComponent> -->
      <el-form :model="caseForm" v-show="radioData[0].value === '1'" :rules="formRules" ref="caseForm" label-width="100px" label-position="left" class="case-form">
        <el-form-item v-for="(item, i) in caseDetails" :label="item.title" :prop="item.key" :key="i">
          <el-input v-if="item.valueType == 'number'" type="number" v-model="caseForm[item.key]"></el-input>
          <el-input v-else v-model="caseForm[item.key]"></el-input>
        </el-form-item>
      </el-form>
      <ControlButton :next-path="nextPath" :is-disabled="!creditStatusStatus" :save="Save"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import ControlButton from '@/components/ControlButton/ControlButton'
import { isPositiveInteger, validatetoFixedNew } from '@/units/validator'

export default {
  name: 'CreditStatus',
  components: {
    InputComponent,
    ControlButton
  },
  data () {
    return {
      surveyRatingData: JSON.parse(localStorage.getItem('surveyRatingData')),
      keyStr: {
        // isMajorLitigation: '上年是否涉诉重大诉讼案件',
        litigationNumber: '上年涉诉案件数量',
        litigationMoney: '上年涉诉案件金额(万元)',
        undoneLitigationNumber: '上年未执行案件数量',
        undoneLitigationMoney: '上年未执行案件金额(万元)'
      },
      radioData: [{
        title: '上年是否涉诉重大诉讼案件',
        value: null,
        isRiequired: true,
        valueType: 'radio'
      }],
      caseDetails: [],
      caseForm: {},
      formRules: {
        litigationNumber: [
          { required: true, message: '请输入上年涉诉案件数量', trigger: 'blur' },
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        litigationMoney: [
          { required: true, message: '请输入上年涉诉案件金额(万元)', trigger: 'blur' },
          { validator: validatetoFixedNew, trigger: 'blur' }
        ],
        undoneLitigationNumber: [
          { required: true, message: '请输入上年未执行案件数量', trigger: 'blur' },
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        undoneLitigationMoney: [
          { required: true, message: '请输入上年未执行案件金额(万元)', trigger: 'blur' },
          { validator: validatetoFixedNew, trigger: 'blur' }
        ]
      },
      nextPath: 'ProfessionalCompetence'
    }
  },
  computed: {
    creditStatusStatus () {
      let status = true
      if (this.radioData[0].value === '1') {
        for (let i in this.keyStr) {
          if (!this.surveyRatingData[i]) status = false
        }
      }
      return status
    }
  },
  created () {
    this.radioData[0].value = this.surveyRatingData.isMajorLitigation
    for (let i in this.keyStr) {
      // let obj = { title: this.keyStr[i], key: i, value: this.surveyRatingData[i] === '0' ? '' : this.surveyRatingData[i], isRiequired: true, valueType: 'number' }
      let obj = { title: this.keyStr[i], key: i, value:this.surveyRatingData[i], isRiequired: true, valueType: 'number' }
      this.caseDetails.push(obj)
      console.log(this.caseDetails)
    }
    let caseForm = {}
    for (let i in this.caseDetails) {
      caseForm[this.caseDetails[i].key] = this.caseDetails[i].value
    }
    this.caseForm = caseForm
    // console.log(this.caseDetails, this.caseForm)
  },
  watch: {
    caseForm: {
      handler (val, oldval) {
        for (let i in val) {
          // console.log(i, val[i])
          this.surveyRatingData[i] = val[i]
        }
      },
      immediate: false,
      deep: true
    }
  },
  methods: {
    Save () {
      this.surveyRatingData.isMajorLitigation = this.radioData[0].value
      let formStatus = true
      // this.radioData[0].value === '1' && this.$refs['caseForm'].validate((valid) => {
      //   if (!valid) {
      //     formStatus = false
      //     return false
      //   }
      // })
      if (formStatus) {
        let data = this.radioData
        for (let i in data) {
          this.surveyRatingData[data[i].key] = data[i].value
        }
        localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
        this.$message.closeAll()
        this.$message({
          message: '草稿保存成功',
          type: 'success',
          // duration: 0,
          center: true
        })
        return true
      }
      return false
    }
  },
  mounted () {
    var tempStatId = localStorage.getItem('statusId') ? localStorage.getItem('statusId') : '00'
    localStorage.setItem('credisStsa' + tempStatId, '1')
  }
}
</script>

<style scoped>
.credit-status .credit-status-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.credit-status .credit-status-main .input-component {
  padding-bottom: 30px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
  justify-content: space-between;
}
.credit-status .credit-status-main .input-component:first-child {
  padding-bottom: 20px;
  margin-bottom: 30px;
}
</style>

<style>
.credit-status-main .input-component .input-component-item {
  min-height: 74px;
}
.credit-status-main .input-component-item strong {
  width: 93px;
}
</style>



