<template>
  <div class="compliance-homepage">
    <h2 class="sub-heading">合规管理</h2>
    <div class="compliance-list">
      <div class="compliance-table">
        <el-table :data="tableData" highlight-current-row ref="singleTable" @cell-click="Operate" :cell-style="cellStyle" v-loading="tableLoading">
          <el-table-column label="单据号" property="id"  ></el-table-column>
          <el-table-column label="单据类型" :formatter="formatterColumn"></el-table-column>
          <el-table-column label="服务商商户号" property="merchantCode" width="190"></el-table-column>
          <el-table-column label="服务商名称" property="companyName" ></el-table-column>
          <el-table-column label="操作状态" :formatter="formatterColumn" width="160"></el-table-column>
          <el-table-column label="操作" :formatter="formatterColumn"  width="160" class-name="operating"></el-table-column>
        </el-table>
      </div>
      <HomemadePagination v-if="total" :page-size="pageSize" :total="total" :current="current" @CurChange="CurChange"></HomemadePagination>
    </div>
  </div>
</template>

<script>
import HomemadePagination from '@/components/HomemadePagination/HomemadePagination'
import util from '@/units/util'

export default {
  components: {
    HomemadePagination
  },
  name: 'ComplianceHomepage',
  data () {
    return {
      complianceUrl: this.$store.state.complianceList.complianceUrl,
      tableLoading: true,
      pageSize: 6,
      current: 1,
      tableData: [],
      total: 0
    }
  },
  created () {
    this.GetTableData()
  },
  computed: {
    mchid () {
      return util.getCookie('merchant_code') || 1900012651
      // return util.getCookie('merchant_code') 
    }
    // tableData () {
    //   return this.$store.state.complianceList.list
    // }
  },
  mounted () {
  },
  methods: {
    Operate (row, column, cell, event) {
      console.log(row, column, cell, event)
      // 1=未填写2=待审核3=已审核4=已驳回
      if (column.label === '操作') {
        let page
        if (row.status === '2' || row.status === '3' || row.status === '4') {
          if (row.status === '4') {
            localStorage.setItem('FRejectReason', row.FRejectReason)
            localStorage.setItem('pushType', row.type === '1' ? 1 : 2)
            page = 'ResultAudit'
          } else {
            // page = row.type === '1' ? 'InspectionListPreview' : 'SurveyRatingPreview'
            var WXPAY_JSLOG_REPORT = null;
            if(row.type === '1' ){
              page = 'InspectionListPreview'
              //上报
              if(WXPAY_JSLOG_REPORT) WXPAY_JSLOG_REPORT.send({common_log:"InspectionListCheck"}); // 查看
             
            }else{
              page = 'SurveyRatingPreview'
              //上报
              if(WXPAY_JSLOG_REPORT) WXPAY_JSLOG_REPORT.send({common_log:"SurveyRatingCheck"});// 查看
           
            }
          }
          localStorage.setItem('hideSubmit', row.status === '4' ? false : true)
        } else {
          // page = row.type === '1' ? 'InspectionList' : 'SurveyRating'
          if(row.type === '1'){
            page = 'InspectionList'
            //上报
            if(WXPAY_JSLOG_REPORT) WXPAY_JSLOG_REPORT.send({common_log:"InspectionListWrite"}); // 填写
          }else{
            page = 'SurveyRating'
            //上报
            if(WXPAY_JSLOG_REPORT) WXPAY_JSLOG_REPORT.send({common_log:"SurveyRatingWrite"}); // 填写
          }

          localStorage.getItem('internalStsa' + row.id) !== '1' ? localStorage.setItem('internalStsa' + row.id, 2) : ''
          localStorage.getItem('credisStsa' + row.id) !== '1' ? localStorage.setItem('credisStsa' + row.id, 2) : ''
          localStorage.getItem('manageStsa' + row.id) !== '1' ? localStorage.setItem('manageStsa' + row.id, 2) : ''
          localStorage.getItem('personalStsa' + row.id) !== '1' ? localStorage.setItem('personalStsa' + row.id, 2) : ''
          localStorage.getItem('RiskControlStsa' + row.id) !== '1' ? localStorage.setItem('RiskControlStsa' + row.id, 2) : ''
          localStorage.getItem('business' + row.id) !== '1' ? localStorage.setItem('business' + row.id, 2) : ''
          localStorage.setItem('hideSubmit', false)
        }
        localStorage.setItem('previewRecordId', row.recordId)
        localStorage.setItem('statusId', row.id)
        // this.$store.commit('setPreviewRecordId', row.recordId)
        this.$router.push(page)
      }
    },
    cellStyle ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 4 && row.status === '4') {
        return 'color: red'
      } else {
        return ''
      }
    },
    CurChange (val) {
      this.current = val
      this.GetTableData()
    },
    formatterColumn (row, column) {
      switch (column.label) {
        case '单据类型':
          return row.type === '1' ? '非现场检查单' : '尽职调查/评级'
        // break
        case '操作状态':
          return ['未填写', '待审核', '已审核', '已驳回'][row.status - 1]
        // break
        case '操作':
          return ['填写', '查看', '查看', '查看'][row.status - 1]
        // break
        default:
          return ''
      }
    },
    GetTableData () {
      this.tableLoading = true
      var params = { mchid: this.mchid, page: this.current, limit: this.pageSize }
      this.$axios.get(this.complianceUrl, { params: params }).then(res => {
        console.log('请求成功:', res)
        if (res && res.data && res.data.return_code === '0') {
          this.tableData = res.data.lists
          this.total = Number(res.data.total)
        } else {
          console.log('请求失败：', res)
          this.tableData = []
          this.total = 0
          this.$message.closeAll()
          this.$message({
            // message: res.data.return_msg,
            message: '登录超时，请刷新页面重新登录',
            type: 'error',
            // duration: 0,
            center: true
          })
        }
      }).catch(res => {
        console.log('res:',res)
        this.$message.closeAll()
        this.$message({
          message: '请求超时,请刷新页面重试!',
          type: 'error',
          // duration: 0,
          center: true
        })
      }).finally(() => { this.tableLoading = false })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.compliance-homepage {
  width: 100%;
  /* width: 100%; */
}
.compliance-homepage .compliance-list {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.compliance-homepage .compliance-list .compliance-table {
  box-sizing: border-box;
  min-height: 395px;
  padding: 20px 0 30px 0;
}
.compliance-homepage .compliance-list .compliance-table .el-table {
  width: 100%;
  min-height: 345px;
  border-top: 1px solid rgb(235, 238, 245);
  margin: 0;
}

.compliance-homepage .compliance-list .compliance-pagination {
  display: flex;
  justify-content: flex-end;
}
</style>

<style>
.compliance-homepage .compliance-list .compliance-table .el-table thead th {
  height: 56px;
  background-color: #fafafa;
}
.compliance-homepage .compliance-list .compliance-table .el-table tbody td {
  color: #333;
}
.compliance-homepage .el-table tbody .operating .cell {
  color: blue;
  cursor: pointer;
}
</style>



