<template>
  <div class="inspection-list">
    <h2 class="sub-heading">非现场检查单</h2>
    <el-alert :title="reminder" v-if="reminder" type="warning" show-icon></el-alert>
    <div class="inspection-main">
      <h3 class="sub-heading">必填信息</h3>
      <div class="required-list">
        <StepBar :stepArr="stepArr" :FillIn="FillIn"></StepBar>
      </div>
      <ControlButton type="ToBack" :is-disabled="!inspectionListStatus" :is-loading="isLoading" :save-submit="SaveSubmit"></ControlButton>
    </div>
  </div>
</template>

<script>
import StepBar from '@/components/StepBar/StepBar'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  components: {
    StepBar,
    ControlButton
  },
  data () {
    return {
      stas: false,
      getInspectionUrl: this.$store.state.complianceList.getInspectionUrl,
      saveInspectionUrl: this.$store.state.complianceList.saveInspectionUrl,
      ecc_csrf_token: this.$store.state.complianceList.ecc_csrf_token,
      isLoading: false,
      reminder: '',
      inspectionListData: {}
    }
  },
  computed: {
    recordId () {
      return localStorage.getItem('previewRecordId')
    },
    enterpriseInfoStatus () {
      let status = true
      let isRiequiredData = ['companyName', 'companyNature', 'agentCertificateType', 'agentCertificateCode', 'legalName', 'legalCertificateType', 'legalCertificateCode', 'agentSignTime', 'agentCertificateStartTime', 'agentCertificatePlace', 'companyPlace']
      for (let i in isRiequiredData) {
        if (!this.inspectionListData[isRiequiredData[i]]) status = false
      }
      return status
    },
    inspectionListStatus () {
      if (!this.stas) {
        return false
      }
      if (this.inspectionListData.legalImage && this.inspectionListData.institutionCertificateImage && this.enterpriseInfoStatus && this.inspectionListData.worksiteImage) {
        return true
      }
      return false
      // let status = true
      // for (let i in this.inspectionListData) {
      //   if (!this.inspectionListData[i] && i !== 'businessScope') status = false
      // }
      // return status
    },
    worksiteImageStatus () {
      let status = false
      var str = this.inspectionListData.worksiteImage
      if (str) {
        var num = 0
        str.replace(/([;，])/g, function ($1) {
          num++
          return $1
        })
        if (num === 3) {
          status = true
        }
      }
      this.stas = status
      return status
    },
    stepArr () {
      // console.log(111, this.inspectionListData.legalImage)
      var flags = false
      if (this.inspectionListData.legalFileListObj !== undefined && this.inspectionListData.mechanismObj !== undefined) {
        if (this.inspectionListData.legalFileListObj.length !== 0 && this.inspectionListData.mechanismObj.length === 1) {
          flags = true
        }
      }
      return [{
        title: '资质信息',
        text: '请上传法定代表人（负责人）及机构证件影印件',
        status: flags && this.inspectionListData.institutionCertificateImage ? 'complete' : '',
        operat: flags && this.inspectionListData.institutionCertificateImage ? '已完成' : '去填写',
        path: 'QualificationInfo'
      }, {
        title: '企业信息',
        text: '请填写公司及服务商相关信息',
        status: this.enterpriseInfoStatus ? 'complete' : '',
        operat: this.enterpriseInfoStatus ? '已完成' : '去填写',
        path: 'EnterpriseInfo'
      }, {
        title: '检查照片',
        text: '请上传办公现场检查照片',
        status: this.worksiteImageStatus ? 'complete' : '',
        operat: this.worksiteImageStatus ? '已完成' : '去填写',
        // operat: this.inspectionListData.worksiteImage ? '已完成' : '去填写',
        path: 'InspectionPhotos'
      }]
    }
  },
  watch: {
  },
  created () {
    let obj = JSON.parse(localStorage.getItem('inspectionListData')) || {}
    if (obj.id !== this.recordId) {
      let params = { recordId: this.recordId }
      this.$axios.get(this.getInspectionUrl, { params: params }).then(res => {
        if (res && res.data && res.data.return_code === '0') {
          let list = res.data.list
          list.statusId = localStorage.getItem('statusId')
          // list.legalImageUrl = list.legalImage
          // list.institutionCertificateImageUrl = list.institutionCertificateImage
          // list.worksiteImageUrl = list.worksiteImage
          this.inspectionListData = list
          this.inspectionListData.agentCertificateType = '1'
          if (list.worksiteImageUrl && list.worksiteImageUrl !== '') {
            this.inspectionListData.fileListObj = JSON.parse(list.worksiteImageUrl)
          }
          if (list.institutionCertificateImageUrl && list.institutionCertificateImageUrl !== '') {
            this.inspectionListData.mechanismObj = JSON.parse(list.institutionCertificateImageUrl)
          }
          if (list.legalImageUrl && list.legalImageUrl !== '') {
            this.inspectionListData.legalFileListObj = JSON.parse(list.legalImageUrl)
          }
          localStorage.setItem('inspectionListData', JSON.stringify(list))
        } else {
          this.$message.closeAll()
          this.$message({
            // message: res.data.return_msg,
            message: '登录超时，请刷新页面重新登录',
            type: 'error',
            // duration: 0,
            center: true
          })
        }
      }).catch(res => {
        this.$message.closeAll()
        this.$message({
          message: '请求超时,请刷新页面重试',
          type: 'error',
          // duration: 0,
          center: true
        })
      }) // .finally(() => { this.tableLoading = false })
    } else {
      this.inspectionListData = obj
    }
  },
  methods: {
    FillIn (path) {
      // console.log(path)
      this.$router.push(path)
    },
    SaveSubmit () {
      this.isLoading = true
      // let params = JSON.parse(JSON.stringify(this.inspectionListData))
      // params.ecc_csrf_token = this.ecc_csrf_token

      if (this.inspectionListData.companyNature === '0') {
        this.$message.error('请选择公司性质')
        return
      }
      if (this.inspectionListData.agentCertificateType === '0') {
        this.$message.error('请选择服务商证照类型')
        return
      }
      if (this.inspectionListData.agentSignTime === '1970-01-01 00:00:01') {
        this.$message.error('请选择服务商签约时间')
        return
      }
      if (this.inspectionListData.agentCertificatePlace === '') {
        this.$message.error('请填写服务商证照注册地')
        return
      }
      if (this.inspectionListData.companyPlace === '') {
        this.$message.error('请填写公司实际办公地')
        return
      }
      if (!this.stas) {
        this.$message.error('请上传三张公司实际办公地图片')
        return
      }
      let params = new FormData() // 创建form对象
      for (let i in this.inspectionListData) {
        params.append(i, this.inspectionListData[i])
      }
      params.append('ecc_csrf_token', this.ecc_csrf_token)
      // console.log(this.inspectionListData, params)
      let config = { headers: { 'Content-Type': 'multipart/form-data' } }
      this.$axios.post(this.saveInspectionUrl, params, config).then(res => {
        // console.log('请求成功', res)
        this.isLoading = false
        if (res && res.data && res.data.return_code === '0') {
          sessionStorage.setItem('submitStatus', true)
          this.$router.push('ResultSubmitted')
        } else {
          sessionStorage.setItem('submitStatus', false)
          this.$message.closeAll()
          this.$message({
            // message: res.data.return_msg,
            message: '请查看数据是否填写完整,或请刷新页面重试',
            type: 'error',
            // duration: 0,
            center: true
          })
        }
      }).catch(res => {
        // console.log('请求出错', res)
        this.isLoading = false
        sessionStorage.setItem('submitStatus', false)
        this.$message.closeAll()
        this.$message({
          message: '请查看数据是否填写完整,或请刷新页面重试',
          type: 'error',
          // duration: 0,
          center: true
        })
      }) // .finally(() => { this.tableLoading = false })
      //上报
      var WXPAY_JSLOG_REPORT = null;
      if(WXPAY_JSLOG_REPORT) WXPAY_JSLOG_REPORT.send({common_log:"InspectionListSubmit"}); // 提交
    }
  }
}
</script>

<style scoped>
.inspection-list {
  /* width: 1200px; */
  width: 100%;
}
.inspection-list .inspection-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.inspection-list .inspection-main .required-list {
  margin-bottom: 60px;
}
</style>

<style>
.inspection-list .el-alert.el-alert--warning {
  background-color: #fef0f0;
  color: #f56c6c;
}
</style>



