<template>
  <div class="business-compliance">
    <h2 class="sub-heading">业务合规</h2>
    <div class="business-compliance-main">
      <el-form :model="requiredForm" :rules="formRules" ref="requiredForm" label-width="100px" label-position="left" class="required-form">
        <el-form-item v-for="(item, i) in requiredinput" :label="item.title" :prop="item.key" :key="i">
          <el-input v-if="item.valueType == 'number'" type="number" v-model="requiredForm[item.key]"></el-input>
          <el-input v-else v-model="requiredForm[item.key]"></el-input>
        </el-form-item>
      </el-form>
      <InputComponent :form-data="requiredField"></InputComponent>
      <InputComponent :form-data="notRequired"></InputComponent>
      <ControlButton :next-path="nextPath" :is-disabled="!businessComplianceStatus" :save="Save"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import ControlButton from '@/components/ControlButton/ControlButton'
import { isPositiveInteger } from '@/units/validator'

export default {
  name: 'BusinessCompliance',
  components: {
    InputComponent,
    ControlButton
  },
  data () {
    return {
      surveyRatingData: JSON.parse(localStorage.getItem('surveyRatingData')),
      keyStr: {
        // conformanceStaffNumber: '合规工作人数',
        isConformanceDepartment: '是否建立合规管理部门',
        isConformanceRegulation: '是否建立合规管理制度',
        isConformanceProcess: '是否建立合规管理流程'
        // isConformanceText: '是否具有标准的合规管理文本'
      },
      requiredinput: [{
        title: '合规工作人数',
        key: 'conformanceStaffNumber',
        value: '',
        isRiequired: true,
        valueType: 'number'
      }],
      requiredForm: {
        conformanceStaffNumber: ''
      },
      requiredField: [],
      notRequired: [{
        title: '是否具有标准的合规管理文本',
        key: 'isConformanceText',
        value: '',
        isRiequired: false,
        valueType: 'radio'
      }],
      formRules: {
        conformanceStaffNumber: [
          { required: true, message: '请输入合规工作人数', trigger: 'blur' },
          { validator: isPositiveInteger, trigger: 'blur' }
        ]
      },
      nextPath: 'PersonalInfoProtection'
    }
  },
  mounted () {
    var tempStatId = localStorage.getItem('statusId') ? localStorage.getItem('statusId') : '00'
    localStorage.setItem('business' + tempStatId, '1')
  },
  computed: {
    businessComplianceStatus () {
      if (!this.requiredForm.conformanceStaffNumber) {
        return false
      }
      let status = true
      for (let i in this.keyStr) {
        if (!this.surveyRatingData[i]) status = false
      }
      return status
    }
  },
  watch: {
    requiredForm: {
      handler (val, oldval) {
        this.surveyRatingData.conformanceStaffNumber = val.conformanceStaffNumber
      },
      immediate: false,
      deep: true
    },
    requiredField: {
      handler (val, oldval) {
        for (let i in val) {
          this.surveyRatingData[val[i].key] = val[i].value
        }
      },
      immediate: false,
      deep: true
    },
    notRequired: {
      handler (val, oldval) {
        this.surveyRatingData.isConformanceText = val[0].value
        // this.notRequired[0].value = val[0].value
      },
      immediate: false,
      deep: true
    }
  },
  created () {
    this.requiredForm.conformanceStaffNumber = this.surveyRatingData.conformanceStaffNumber
    this.notRequired[0].value = this.surveyRatingData.isConformanceText
    for (let i in this.keyStr) {
      let obj = { title: this.keyStr[i], key: i, value: this.surveyRatingData[i], isRiequired: true, valueType: 'radio' }
      this.requiredField.push(obj)
    }
  },
  methods: {
    Save () {
      let formStatus = true
      // this.$refs['requiredForm'].validate((valid) => {
      //   if (!valid) {
      //     formStatus = false
      //     return false
      //   }
      // })
      if (formStatus) {
        localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
        this.$message.closeAll()
        this.$message({
          message: '草稿保存成功',
          type: 'success',
          // duration: 0,
          center: true
        })
        return true
      }
      return false
    }
  }
}
</script>

<style scoped>
.business-compliance .business-compliance-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.business-compliance .business-compliance-main .required-form .el-form-item {
  padding: 15px 0 0 0;
  margin: 0;
}
.business-compliance .business-compliance-main .input-component {
  padding-bottom: 30px; 
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
  justify-content: space-between;
}
.business-compliance .business-compliance-main .input-component:nth-child(2) {
  /* padding-bottom: 20px; */
  padding-bottom: 0;
  border-bottom: none;
  margin: -85px 0 20px 0;
}
</style>

<style>
.business-compliance-main .input-component .input-component-item {
  min-height: 74px;
}
.business-compliance-main .input-component-item strong {
  width: 93px;
}
.business-compliance
  .business-compliance-main
  .input-component:nth-child(2)
  .input-component-item:nth-child(1) {
  margin-left: calc(100% - 320px);
}

.business-compliance-main .input-component .input-component-item {
    
 
}

/* .business-compliance-main .input-component div:nth-child(1) .riequired{
  margin-left: 140px;
} */

</style>





