<template>
  <div class="control-button">
    <div v-if="type == 'SaveToback'">
      <el-button type="info" plain @click="saveToback">保存并返回</el-button>
      <el-button type="success" @click="saveSubmit" :disabled="isDisabled">预览</el-button>
    </div>
    <div v-else-if="type == 'SaveSubmit'">
      <el-button type="success" @click="saveSubmit" :disabled="isDisabled" :loading="isLoading">{{isLoading ? '正在提交' : '保存并提交'}}</el-button>
    </div>
    <div v-else-if="type == 'ConfirmSubmit'">
      <el-button type="info" plain @click="Toback">{{hideSubmit ? '返回' : '上一步'}}</el-button>
      <el-button type="success" @click="saveSubmit" :disabled="isDisabled" :loading="isLoading" v-if="!hideSubmit">{{isLoading ? '正在提交' : '确认提交'}}</el-button>
    </div>
    <div v-else-if="type == 'ToBack'">
      <el-button type="info" plain @click="TobackHome">返回</el-button>
        <el-button type="success" @click="saveSubmit" :disabled="isDisabled" :loading="isLoading">{{isLoading ? '正在提交' : '保存并提交'}}</el-button>
    </div>
    <div v-else>
      <el-button type="info" plain @click="Toback">返回</el-button>
      <el-button type="info" plain @click="save">保存草稿</el-button>
      <el-button type="success" @click="NextPage" :disabled="isDisabled">下一步</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ControlButton',
  props: {
    type: {
      default: null,
      type: String
    },
    backNum: {
      default: -1
    },
    nextPath: {
      // required: true,
      type: String
    },
    isDisabled: {
      default: true,
      type: Boolean
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    hideSubmit: {
      default: false,
      type: Boolean
    },
    save: {
      type: Function,
      default () { }
    },
    sa: {
      type: Function,
      default () { }
    },
    saveToback: {
      type: Function,
      default () { }
    },
    saveSubmit: {
      type: Function,
      default () {
        this.$router.push({ name: 'ResultSubmitted', params: { status: 1 } })
      }
    }
  },
  data () {
    return {
    }
  },
  methods: {
    Toback () {
      // this.save()
      this.$router.go(this.backNum)
    },
    TobackHome () {
      // this.$router.push('/');
      this.$router.push('/serviceProvideCheck');
    },
    // Save () {
    //   this.$message.closeAll()
    //   this.$message({
    //     message: '草稿保存成功',
    //     type: 'success',
    //     // duration: 0,
    //     center: true
    //   })
    // },
    NextPage () {
      this.save() !== false && this.$router.push(this.nextPath)
    }
  }
}
</script>

<style>
/* 按钮 */
.control-button .el-button {
  width: 120px;
}
.control-button .el-button--info.is-plain {
  color: #555;
  border: none;
  background: #eee;
}
.control-button .el-button--info.is-plain.is-disabled {
  color: #bcbec2 !important;
  background-color: #f4f4f5 !important;
  border-color: #e9e9eb;
}
/* .control-button .el-button--info.is-plain:focus, */
.control-button .el-button--info.is-plain:hover {
  background: #909399;
  color: #fff;
}
.control-button .el-button--info.is-plain:active {
  background: #82848a;
  border-color: #82848a;
  color: #fff;
}
.control-button .el-button.el-button--success {
  color: #fff;
  background-color: #00c250;
  border-color: #00c250;
}
.control-button .el-button.el-button--success:hover {
  background: limegreen;
  border-color: limegreen;
  color: #fff;
}
.control-button .el-button.el-button--success:active {
  background: #5daf34;
  border-color: #5daf34;
  color: #fff;
}
.control-button .el-button.el-button--success.is-disabled,
.control-button .el-button.el-button--success.is-disabled:active,
.control-button .el-button.el-button--success.is-disabled:focus,
.control-button .el-button.el-button--success.is-disabled:hover {
  color: #fff;
  background-color: #b3e19d;
  border-color: #b3e19d;
}
</style>



