<template>
  <div class="input-component">
    <div class="input-component-item" :class="item.title === '主要职责 ' || item.title === '高级管理人员职务 '? 'topBoder' : ''" v-for="(item, i) in formData" :key="i" :style="item.valueType === 'picture-card' ? 'display: none;' : ''">
      <strong :class="{'riequired': item.isRiequired && isInput}">{{item.title}}</strong>
      <span v-if="!isInput && item.valueType === 'file' && !item.url" class="file-preview" @click="fileDownload(item.url)">
        <div class="el-icon-document" style="margin-right: 6px;" v-if="item.value"></div>{{item.value}}
      </span>
      <span v-else-if="!isInput && item.valueType === 'files' && item.url" class="file-preview" v-for="(row, index) in item.value" :key="index">
        <div class="el-icon-document" style="margin-right: 6px;" v-if="item.value">
          <a :href="row.url" target="_blank"> {{row.name}}</a>
        </div>
      </span>
      <span v-else-if="!isInput && item.valueType === 'filesteam'" class="file-preview">
          <a :href="item.url" target="_blank"> {{item.value}}</a>
      </span>
      <span v-else-if="!isInput">{{valueText(item)}}</span>
      <el-date-picker v-else-if="item.valueType === 'date'" v-model="item.value" type="date" placeholder="选择日期" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
      <el-date-picker v-else-if="item.valueType === 'daterange'" v-model="item.value" type="daterange" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
      <el-input v-else-if="item.valueType === 'number'" :placeholder="item.isRiequired ? '请输入' : '(选填) 请输入'" type="number" v-model="item.value" clearable></el-input>
      <el-radio-group v-else-if="item.valueType === 'radio'" v-model="item.value">
        <el-radio label="1">是</el-radio>
        <el-radio label="0">否</el-radio>
      </el-radio-group>
      <el-select v-else-if="item.valueType === 'select'" v-model="item.value" placeholder="请选择">
        <el-option v-for="(item1, i1) in item.options" :key="i1" :label="item1.text" :value="item1.value" :value-key="item1.value"></el-option>
      </el-select>
      <el-input v-else-if="item.valueType === 'textarea'" v-model="item.value" rows="15" type="textarea" placeholder="(选填)请输入" maxlength="300" show-word-limit resize="none"></el-input>
      <el-input v-else :placeholder="item.isRiequired ? '请输入' : '(选填) 请输入'" v-model="item.value" clearable></el-input>
    </div>
  </div>
</template>

<script>
export default {
  name: 'InputComponent',
  props: {
    formData: {
      required: true,
      type: Array
    },
    isInput: {
      default: true
    }
  },
  methods: {
    valueText (item) {
        let text = Array.isArray(item.value) ? item.value[0] + ' - ' + item.value[1] : item.valueType === 'select' ? item.valText[item.value] : item.valueType === 'radio' ? (item.value === '1' ? '是' : '否') : item.value
      return text
    },
    fileDownload (url) {
      console.log(url, '12312321')
      // url && (window.open(url, '_blank'))
    }
  }
}
</script>

<style>
.input-component {
  display: flex;
  flex-wrap: wrap;
}
.input-component-item {
  word-wrap:break-word;
  word-break:break-all;
  width: 500px;
  min-height: 60px;
  display: flex;
  align-items: center;
}
.input-component-item strong {
  width: 79px;
  flex: none;
  display: flex;
  margin-right: 7px;
  line-height: 1.4;
  font-weight: 600;
}
.input-component-item strong.riequired:after {
  content: '*';
  color: red;
  flex: none;
  margin-left: 3px;
}
.input-component-item .el-textarea__inner {
  height: 120px;
}
.input-component .file-preview {
  cursor: pointer;
  padding: 3px 6px;
}
.input-component .file-preview:hover {
  background: #eee;
}
.topBoder{
  border-top: 0.5px dashed #ddd!important;
}
</style>



