// 非现场检查单数据
const state = {
  // 法人证件
  legalPersonFile: [],
  // 机构证件
  mechanismFile: [],
  // 企业信息待填项
  enterpriseUnfillData: [],
  // 企业信息已填项
  enterpriseFillData: [],
  // 检查照片
  inspectionPhotos: []
}
const getters = {
  // inspectionListData: state => JSON.parse(localStorage.getItem('inspectionListData')),
  // legalPersonFile: (state, getters) => getters.inspectionListData.legalImage,
  // mechanismFile: state => state.mechanismFile,
  // 资质信息完成状态
  // qualificationInfoStatus (state, getters) {
  //   return getters.inspectionListData.legalImage && getters.inspectionListData.institutionCertificateImage
  // },
  // enterpriseUnfillData: state => state.enterpriseUnfillData,
  // enterpriseFillData: state => state.enterpriseFillData,
  // 企业信息完成状态
  // enterpriseInfoStatus (state, getters) {
  //   return false
  // }
  // inspectionPhotos: state => state.inspectionPhotos
}
const mutations = {
  setLegalPersonFile (state, legalPersonFile) {
    state.legalPersonFile = legalPersonFile
  },
  setMechanismFile (state, mechanismFile) {
    state.mechanismFile = mechanismFile
  },
  setEnterpriseUnfillData (state, enterpriseUnfillData) {
    state.enterpriseUnfillData = enterpriseUnfillData
  },
  setEnterpriseFillData (state, enterpriseFillData) {
    state.enterpriseFillData = enterpriseFillData
  }
}
const actions = {
  saveLegalPersonFile ({ state, commit }, obj) {
    commit('setLegalPersonFile', obj)
  },
  saveMechanismFile ({ state, commit }, obj) {
    commit('setMechanismFile', obj)
  },
  saveEnterpriseUnfillData ({ state, commit }, obj) {
    commit('setEnterpriseUnfillData', obj)
  },
  saveEnterpriseFillData ({ state, commit }, obj) {
    commit('setEnterpriseFillData', obj)
  }
}
export default {
  state,
  getters,
  mutations,
  actions
}



