<template>
  <div class="operating-conditions">
    <h2 class="sub-heading">经营状况</h2>
    <div class="operating-conditions-main">
      <h3 class="sub-heading">待填项</h3>
      <div class="unfilled">
        <!-- <InputComponent :form-data="unfillData"></InputComponent> -->
        <el-form :model="unfillForm" :rules="formRules" ref="unfillForm" label-width="100px" label-position="left" class="unfill-form">
          <el-form-item v-for="(item, i) in unfillData" :label="item.title" :prop="item.key" :key="i">
            <el-select v-if="item.valueType == 'select'" v-model="unfillForm[item.key]">
              <el-option v-for="(itemj, j) in item.options" :label="itemj.text" :value="itemj.value" :key="j"></el-option>
            </el-select>
            <el-date-picker v-else-if="item.valueType == 'date'" type="date" placeholder="选择日期" v-model="unfillForm[item.key]" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            <el-date-picker v-else-if="item.valueType === 'daterange'" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" v-model="unfillForm[item.key]" range-separator="-" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            <!-- <el-input v-else-if="item.valueType === 'number'" type="number" :placeholder="item.placeholder" v-model="unfillForm[item.key]" onkeyup="value=value.replace(/^\D*(\d*(?:\.\d{0,2})?).*$/g, '$1')"></el-input> -->
            <el-input v-else-if="item.valueType === 'number'" type="number" :placeholder="item.placeholder" v-model="unfillForm[item.key]" oninput="value=value.replace(/^\D*(\d*(?:\.\d{0,2})?).*$/g, '$1')"></el-input>
            <el-input v-else v-model="unfillForm[item.key]"></el-input>
          </el-form-item>
        </el-form>
        <div class="picture-preview">
          <FileUploadMore v-if="!hasLegalImage" :tip-text="legalPerson.tipText" :title="legalPerson.title" :is-riequired="legalPerson.isRiequired" :name="legalPerson.name" :file-limit="legalPerson.fileLimit" :isPreview="false" list-type="picture-card" :file-list-prop="legalPerson.fileList" :handle-avatar-success="legalSuccess" @fileRemove= "(file, fileLists)=> fileRemove(file, fileLists,1)"></FileUploadMore>
          <FileUploadMore v-if="!hasBusinessLicenseImage" :tip-text="businessLicense.tipText" :title="businessLicense.title" :is-riequired="businessLicense.isRiequired" :file-limit="businessLicense.fileLimit" :name="businessLicense.name" :isPreview="false" list-type="picture-card" :file-list-prop="businessLicense.fileList" :handle-avatar-success="businessSuccess" @fileRemove= "(file, fileLists)=> fileRemove(file, fileLists,2)"></FileUploadMore>
        </div>
      </div>
      <h3 class="sub-heading">已填项</h3>
      <div class="input-enable" @click="InputEnable">{{isInput ? "取消修改" : "修改"}}</div>
      <div class="filled">
        <!-- <InputComponent :form-data="fillData" :is-input="isInput"></InputComponent> -->
        <el-form :model="fillForm" :rules="formRules" ref="fillForm" label-width="100px" label-position="left" class="fill-form">
          <el-form-item v-for="(item, i) in fillData" :label="item.title" :prop="item.key" :key="i">
            <p v-if="!isInput">{{valueText(item, fillForm[item.key])}}</p>
            <el-select v-else-if="item.valueType == 'select'" v-model="fillForm[item.key]">
              <el-option v-for="(itemj, j) in item.options" :label="itemj.text" :value="itemj.value" :key="j + itemj.text"></el-option>
            </el-select>
            <el-date-picker v-else-if="item.valueType == 'date'" type="date" placeholder="选择日期" v-model="fillForm[item.key]" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            <el-date-picker v-else-if="item.valueType == 'daterange'" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" v-model="fillForm[item.key]" range-separator="-" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            <el-input v-else-if="item.valueType == 'number'" type="number" v-model="fillForm[item.key]"></el-input>
            <el-input v-else v-model="fillForm[item.key]"></el-input>
          </el-form-item>
        </el-form>
        <div class="picture-preview">
          <FileUploadMore v-if="hasLegalImage" :tip-text="legalPerson.tipText" :title="legalPerson.title" :is-riequired="legalPerson.isRiequired" :file-limit="legalPerson.fileLimit" :name="legalPerson.name" :isPreview="false && !isInput" :preview-url="surveyRatingData.legalImageUrl" list-type="picture-card" :file-list-prop="legalPerson.fileList" :handle-avatar-success="legalSuccess" @fileRemove= "(file, fileLists)=> fileRemove(file, fileLists,1)"></FileUploadMore>
          <FileUploadMore v-if="hasBusinessLicenseImage" :tip-text="businessLicense.tipText"  :title="businessLicense.title" :file-limit="businessLicense.fileLimit" :is-riequired="businessLicense.isRiequired" :name="businessLicense.name" :isPreview="false && !isInput" :preview-url="surveyRatingData.businessLicenseImageUrl" list-type="picture-card" :file-list-prop="businessLicense.fileList" :handle-avatar-success="businessSuccess" @fileRemove= "(file, fileLists)=> fileRemove(file, fileLists,2)"></FileUploadMore>
        </div>
      </div>
      <ControlButton :next-path="nextPath" :is-disabled="!operatingConditionStatus" :save="Save"></ControlButton>
    </div>
  </div>

</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import FileUploadMore from '@/components/FileUpload/FileUploadMore'
import ControlButton from '@/components/ControlButton/ControlButton'
import { validateIdTwoNo, validatePhoneTwo, validatetoFixedTwoNew, isBtnZeroToHundred, isPositiveInteger } from '@/units/validator'

export default {
  name: 'OperatingConditions',
  components: {
    InputComponent,
    FileUploadMore,
    ControlButton
  },
  data () {
    var checkContactName = (rule, value, callback) => {
      if (!value) {
        this.flag = 1
        return callback(new Error('请输入联系人姓名'))
      } else if (value.length > 64) {
        this.flag = 1
        return callback(new Error('不能超过64位'))
      } else {
        this.flag = 2
        callback()
      }
    }
    var checkWorksiteAddress = (rule, value, callback) => {
      if (!value) {
        this.flag = 2
        callback()
      } else if (value.length > 128) {
        this.flag = 1
        return callback(new Error('不能超过128位'))
      } else {
        this.flag = 2
        callback()
      }
    }
    var checkStockholderName = (rule, value, callback) => {
      if (!value) {
        this.flag = 2
        callback()
      } else if (value.length > 128) {
        this.flag = 1
        return callback(new Error('股东名称长度不能超过128位'))
      } else {
        this.flag = 2
        callback()
      }
    }
    var validateIdTwoNore = (rule, value, callback) => {
      validateIdTwoNo(value, '请输入法定代表人（负责人）身份证号', '请输入正确的法定代表人（负责人）身份证号', callback)
    }
    var validatePhoneTwore = (rule, value, callback) => {
      validatePhoneTwo(value, '请输入联系手机号码', '请输入正确的联系手机号码', callback)
    }
    var validatetoNewFixed = (rule, value, callback) => {
      validatetoFixedTwoNew(value, 16, '请输入正确格式的实缴货币资本', callback)
    }
    var validatetoFixedReNew = (rule, value, callback) => {
      validatetoFixedTwoNew(value, 16, '请输入正确格式的注册资本', callback)
    }
    return {
      businessObj: [],
      legalPersonObj: [],
      flag: 0,
      surveyRatingData: JSON.parse(localStorage.getItem('surveyRatingData')),
      keyStr: {
        companyName: '公司名称',
        companyType: '公司类型',
        companyCode: '营业执照注册号',
        businessLicensePlace: '注册地址',
        legalName: '法定代表人（负责人）姓名',
        legalCertificateCode: '法定代表人（负责人）身份证号',
        contactName: '联系人',
        contactTel: '联系电话',
        worksiteAddress: '办公地址',
        registeredCapital: '注册资本(万元)',
        moneyCapital: '实缴货币资本(万元)',
        legalWorkYear: '法定代表人（负责人）人工作年限',
        legalEducation: '法定代表人（负责人）学历',
        legalGraduationTime: '法定代表人（负责人）最高学历毕业时间',
        legalCollege: '法定代表人（负责人）最高学历毕业院校',
        legalMajor: '法定代表人（负责人）最高学历毕业专业',
        legalBadCreditNumber: '法定代表人（负责人）不良信用记录次数',
        managerNumber: '学历在本科及以上的高级管理人员数量',
        stockholderName: '控股股东名称',
        stockholderRatio: '控股股东持股比例(%)'
      },
      isRiequiredData: ['companyName', 'companyType', 'companyCode', 'businessLicensePlace', 'legalName', 'legalCertificateCode', 'contactName', 'contactTel'],
      isNumberData: ['contactTel', 'registeredCapital', 'moneyCapital', 'legalWorkYear', 'legalBadCreditNumber', 'managerNumber', 'stockholderRatio'],
      // isFillDataData: ['companyName', 'companyType', 'companyCode', 'businessLicensePlace', 'legalName', 'legalCertificateCode'],
      unfillData: [],
      unfillForm: {},
      fillData: [],
      fillForm: {},
      formRules: {
        stockholderName: [
          { required: false, message: '股东名称长度不能超过128位', trigger: 'blur' },
          { validator: checkStockholderName, trigger: 'blur' }
        ],
        worksiteAddress: [
          { required: false, message: '办公地址不能超过128位', trigger: 'blur' },
          { validator: checkWorksiteAddress, trigger: 'blur' }
        ],
        companyName: [
          { required: true, message: '请输入公司名称', trigger: 'blur' }
        ],
        companyType: [
          { required: true, message: '请选择公司类型', trigger: 'change' }
        ],
        companyCode: [
          { required: true, message: '请输入营业执照注册号', trigger: 'blur' }
        ],
        businessLicensePlace: [
          { required: true, message: '请输入注册地址', trigger: 'blur' }
        ],
        legalName: [
          { required: true, message: '请输入法定代表人（负责人）姓名', trigger: 'blur' }
        ],
        legalCertificateCode: [
          // { required: true, message: '请输入法定代表人身份证号', trigger: 'blur' },
          { required: true, validator: validateIdTwoNore, trigger: 'blur' }
        ],
        contactName: [
          // { required: true, message: '请输入联系人姓名', trigger: 'blur' },
          { required: true, validator: checkContactName, trigger: 'blur' }
        ],
        contactTel: [
          // { required: true, message: '请输入联系手机号码', trigger: 'blur' },
          { required: true, validator: validatePhoneTwore, trigger: 'blur' }
        ],
        registeredCapital: [
          { validator: validatetoFixedReNew, trigger: 'blur' }
        ],
        moneyCapital: [
          { validator: validatetoNewFixed, trigger: 'blur' }
        ],
        legalWorkYear: [
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        legalBadCreditNumber: [
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        managerNumber: [
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        stockholderRatio: [
          { 
            validator: isBtnZeroToHundred, 
             trigger: 'blur' 
          }
        ]
      },
      hasLegalImage: false,
      hasBusinessLicenseImage: false,
      isInput: false,
      initFillForm: '',
      nextPath: 'ManagementTeam'
    }
  },
  computed: {
    legalPerson () {
      // var fileList = this.surveyRatingData.legalImage ? [{ name: '法定代表人证件影印件', url: this.surveyRatingData.legalImageUrl }] : []
      var obj = []
      if (this.legalPersonObj.length > 0) {
        obj = this.legalPersonObj
      } else {
        for (var i in this.surveyRatingData.legalPersonObj) {
          obj.push({name: this.surveyRatingData.legalPersonObj[i] ? this.surveyRatingData.legalPersonObj[i].name : '',
            url: this.surveyRatingData.legalPersonObj[i] ? this.surveyRatingData.legalPersonObj[i].url : ''})
        }
      }
      return {
        name: 'legalImage',
        fileLimit: 2,
        title: '法定代表人（负责人）证件影印件',
        isRiequired: true,
        fileList: obj,
        tipText: '身份证用户需上传正反面影印件,图片建议尺寸:128X128像素,大小不超过2M,支持JPG、PNG格式'
      }
    },
    businessLicense () {
      // var fileList = this.surveyRatingData.businessLicenseImage ? [{ name: '机构证件影印件', url: this.surveyRatingData.businessLicenseImageUrl }] : []
      var obj = []
      if (this.businessObj.length > 0) {
        obj = this.businessObj
      } else {
        for (var i in this.surveyRatingData.businessObj) {
          obj.push({name: this.surveyRatingData.businessObj[i] ? this.surveyRatingData.businessObj[i].name : '',
            url: this.surveyRatingData.businessObj[i] ? this.surveyRatingData.businessObj[i].url : ''})
        }
      }
      return {
        name: 'businessLicenseImage',
        title: '营业执照扫描件',
        fileLimit: 1,
        isRiequired: true,
        fileList: obj,
        tipText: '营业执照1张,图片建议尺寸:128X128像素,大小不超过2M,支持JPG、PNG格式'
      }
    },
    operatingConditionStatus () {
      var a = 0
      if (this.flag !== 2) {
        if (this.flag === 1) {
          return false
        }
        if (this.fillData === []) {
          return false
        }
        this.fillData.forEach((item, index) => {
          if (item.key === 'contactName' && item.value.length > 64) { a = 1 }
          if (item.key === 'worksiteAddress' && item.value.length > 128) { a = 1 }
          if (item.key === 'moneyCapital' && item.value.length > 16) { a = 1 }
          if (item.key === 'registeredCapital' && item.value.length > 16) { a = 1 }
          if (item.key === 'stockholderName' && item.value.length > 128) { a = 1 }
          if (item.key === 'contactTel' && item.value.length > 11) { a = 1 }
        })
        // console.log('a:', a)
        this.unfillData.forEach((item, index) => {
          if (item.key === 'contactName' && item.value.length > 64) { a = 1 }
          if (item.key === 'worksiteAddress' && item.value.length > 128) { a = 1 }
          if (item.key === 'moneyCapital' && item.value.length > 16) { a = 1 }
          if (item.key === 'registeredCapital' && item.value.length > 16) { a = 1 }
          if (item.key === 'stockholderName' && item.value.length > 128) { a = 1 }
          if (item.key === 'contactTel' && item.value.length > 11) { a = 1 }
        })
      }
      if (a) {
        return false
      }
      let status = Boolean(this.legalPerson.fileList.length) && Boolean(this.businessLicense.fileList.length)
      for (let i in this.isRiequiredData) {
        if (!this.surveyRatingData[this.isRiequiredData[i]]) status = false
      }
      return status
    }
  },
  watch: {
    unfillForm: {
      handler (val, oldval) {
        this.fillData.forEach((item, index) => {
          if (item.key === 'contactName' && item.value.length > 64) { this.flag = 1 }
          if (item.key === 'worksiteAddress' && item.value.length > 128) { this.flag = 1 }
          if (item.key === 'moneyCapital' && item.value.length > 16) { this.flag = 1 }
          if (item.key === 'registeredCapital' && item.value.length > 16) { this.flag = 1 }
          if (item.key === 'stockholderName' && item.value.length > 128) { this.flag = 1 }
          if (item.key === 'contactTel' && item.value.length > 11) { this.flag = 1 }
        })
        this.unfillData.forEach((item, index) => {
          if (item.key === 'contactName' && item.value.length > 64) { this.flag = 1 }
          if (item.key === 'worksiteAddress' && item.value.length > 128) { this.flag = 1 }
          if (item.key === 'moneyCapital' && item.value.length > 16) { this.flag = 1 }
          if (item.key === 'registeredCapital' && item.value.length > 16) { this.flag = 1 }
          if (item.key === 'stockholderName' && item.value.length > 128) { this.flag = 1 }
          if (item.key === 'contactTel' && item.value.length > 11) { this.flag = 1 }
        })
        this.Update(val)
      },
      immediate: false,
      deep: true
    },
    fillForm: {
      handler (val, oldval) {
        this.Update(val)
      },
      immediate: false,
      deep: true
    }
  },
  mounted () {
    var a = localStorage.getItem('contactNameLenth')
    if (a === '1') {
      this.validate()
    }
  },
  created () {
    this.hasLegalImage = this.surveyRatingData.legalImage
    this.hasBusinessLicenseImage = this.surveyRatingData.businessLicenseImage
    for (let i in this.keyStr) {
      let obj = { title: this.keyStr[i], value: this.surveyRatingData[i], key: i, isRiequired: false, valueType: 'text' }
      if (this.isRiequiredData.indexOf(i) !== -1) obj.isRiequired = true
      if (this.isNumberData.indexOf(i) !== -1) obj.valueType = 'number'
      switch (i) {
        case 'companyType': // 公司类型
          obj.valText = ['', '国有企业', '民营企业', '个体企业', '外资企业', '港澳台企业、中外合资企业', '其他']
          obj.value === '0' && (obj.value = '')
          obj.options = [{ text: '国有企业', value: '1' }, { text: '民营企业', value: '2' }, { text: '个体企业', value: '3' }, { text: '外资企业', value: '4' }, { text: '港澳台企业、中外合资企业', value: '5' }, { text: '其他', value: '6' }]
          obj.valueType = 'select'
          break
        case 'legalEducation': // 法定代表人学历
          obj.valText = ['', '硕士及以上', '本科', '大专', '大专以下']
          obj.value === '0' && (obj.value = '')
          obj.options = [{ text: '硕士及以上', value: '1' }, { text: '本科', value: '2' }, { text: '大专', value: '3' }, { text: '大专以下', value: '4' }]
          obj.valueType = 'select'
          break
        case 'legalGraduationTime': // 法定代表人最高学历毕业时间
          if (obj.value === '1970-01-01 00:00:01') { obj.value = '' } else { obj.value = obj.value.slice(0, 10) }
          obj.valueType = 'date'
          break
        case 'registeredCapital':
          obj.value === '0' && (obj.value = '')
          obj.placeholder = '长度不能超过16位，最多保留两位小数'
          break
        case 'moneyCapital':
          obj.value === '0' && (obj.value = '')
          obj.placeholder = '长度不能超过16位，最多保留两位小数'
          break
        case 'legalWorkYear': case 'stockholderRatio':
          obj.value === '0' && (obj.value = '')
          break
        default:
          break
      }
      if (obj.value) {
        if ((obj.key === 'contactName' && obj.value.length > 64) ||
          (obj.key === 'worksiteAddress' && obj.value.length > 128) ||
          (obj.key === 'stockholderName' && obj.value.length > 128) ||
          (obj.key === 'registeredCapital' && obj.value.length > 16) ||
          (obj.key === 'contactTel' && obj.value.length > 11) ||
          (obj.key === 'moneyCapital' && obj.value.length > 16)) {
          this.unfillData.push(obj)
        } else {
          this.fillData.push(obj)
        }
      } else {
        this.unfillData.push(obj)
      }
    }
    // console.log(this.unfillData, this.fillData)
    let unfillForm = {}
    let fillForm = {}
    for (let i in this.unfillData) {
      unfillForm[this.unfillData[i].key] = this.unfillData[i].value
    }
    for (let i in this.fillData) {
      fillForm[this.fillData[i].key] = this.fillData[i].value
    }
    this.unfillForm = unfillForm
    this.fillForm = fillForm
  },
  methods: {

    validate () {
      this.$refs.unfillForm.validate((valid) => {
        if (valid) {
          return false
        }
      })
    },
    valueText (item, value) {
      let text = Array.isArray(value) ? value[0] + ' - ' + value[1] : item.valueType === 'select' ? item.valText[value] : item.valueType === 'radio' ? (value === '1' ? '是' : '否') : value
      return text
    },
    InputEnable () {
      if (!this.isInput) {
        this.initFillForm = JSON.stringify(this.fillForm)
      } else {
        this.fillForm = JSON.parse(this.initFillForm)
        let _this = this
        setTimeout(() => {
          _this.$refs['fillForm'].validate((valid) => {
            if (!valid) {
              return false
            }
          })
        }, 50)
      }
      this.isInput = !this.isInput
    },
    legalSuccess (res, file, fileList) {
      // console.log('res:',res,'file:', file,'fileList:', fileList)
      this.surveyRatingData.legalImage += res.fname + ';'
      this.surveyRatingData.legalImageUrl = res.furl
      this.legalPersonObj = this.legalPerson.fileList.length > 0 ? this.legalPerson.fileList : []
      this.legalPersonObj.push({name: res.fname, url: res.furl})
      this.surveyRatingData.legalPersonObj = this.legalPersonObj
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
    },
    businessSuccess (res, file, fileList) {
      this.surveyRatingData.businessLicenseImage += res.fname + ';'
      this.surveyRatingData.businessLicenseImageUrl = res.furl
      this.businessObj = this.businessLicense.fileList.length > 0 ? this.businessLicense.fileList : []
      this.businessObj.push({name: res.fname, url: res.furl})
      this.surveyRatingData.businessObj = this.businessObj
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
    },
    fileRemove (file, fileList, num) {
      switch (parseInt(num)) {
        case 1:
          for (let i in this.surveyRatingData.legalPersonObj) {
            if (file.response) {
              if (this.surveyRatingData.legalPersonObj[i].name === file.response.fname) {
                this.surveyRatingData.legalPersonObj.splice(i, 1)
              }
            } else if (this.surveyRatingData.legalPersonObj[i].name === file.name) {
              this.surveyRatingData.legalPersonObj.splice(i, 1)
            }
          }
          var tempArr = ''
          for (let y in this.surveyRatingData.legalPersonObj) {
            tempArr += this.surveyRatingData.legalPersonObj[y].name + ';'
          }
          this.surveyRatingData.legalImage = tempArr
          break
        case 2:
          for (var index in this.surveyRatingData.businessObj) {
            if (file.response) {
              if (this.surveyRatingData.businessObj[index].name === file.response.fname) {
                this.surveyRatingData.businessObj.splice(index, 1)
              }
            } else if (this.surveyRatingData.businessObj[index].name === file.name) {
              this.surveyRatingData.businessObj.splice(index, 1)
            }
          }
          var inspeArr = ''
          for (let m in this.surveyRatingData.businessObj) {
            inspeArr += this.surveyRatingData.businessObj[m].name + ';'
          }
          this.surveyRatingData.businessLicenseImage = inspeArr
          break
      }
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
    },
    Save () {
      this.isInput = false
      let fla = true
      if (this.surveyRatingData.contactName) {
        if (this.surveyRatingData.contactName.length > 64) {
          fla = false
          localStorage.setItem('contactNameLenth', 1)
        }
      }
      if (this.surveyRatingData.worksiteAddress) {
        if (this.surveyRatingData.worksiteAddress.length > 128) {
          fla = false
          localStorage.setItem('contactNameLenth', 1)
        }
      }
      if (this.surveyRatingData.stockholderName) {
        if (this.surveyRatingData.stockholderName.length > 128) {
          fla = false
          localStorage.setItem('contactNameLenth', 1)
        }
      }
      if (this.surveyRatingData.moneyCapital) {
        if (this.surveyRatingData.moneyCapital.length > 16) {
          fla = false
          localStorage.setItem('contactNameLenth', 1)
        }
      }
      if (this.surveyRatingData.registeredCapital) {
        if (this.surveyRatingData.registeredCapital.length > 16) {
          fla = false
          localStorage.setItem('contactNameLenth', 1)
        }
      }
      if (this.surveyRatingData.contactTel) {
        var phone = this.surveyRatingData.contactTel
        if (!(/^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/.test(phone))) {
          this.surveyRatingData.contactTel = ''
          fla = false
          this.surveyRatingData.contactTel = ''
          localStorage.setItem('contactNameLenth', 1)
        }
      }
      if (this.surveyRatingData.stockholderRatio) {
        if (this.surveyRatingData.stockholderRatio > 100 || this.surveyRatingData.stockholderRatio < 0) {
          fla = false
          this.surveyRatingData.stockholderRatio = ''
          localStorage.setItem('contactNameLenth', 1)
        }
      }
      if (fla) {
        localStorage.setItem('contactNameLenth', 2)
      }
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
      this.$message.closeAll()
      this.$message({
        message: '草稿保存成功',
        type: 'success',
        center: true
      })
      return true
    },
    Update (data) {
      for (let i in data) {
        if (i === 'legalGraduationTime') {
          this.surveyRatingData.legalGraduationTime = data[i] ? data[i] + ' 00:00:01' : ''
        } else {
          this.surveyRatingData[i] = data[i]
        }
      }
    }
  }
}
</script>

<style scoped>
.operating-conditions {
  width: 870px!important;
  /* margin: 0 auto; */
}
.operating-conditions .operating-conditions-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
  margin-bottom: 100px;
}
.operating-conditions .operating-conditions-main .input-enable {
  width: max-content;
  color: cornflowerblue;
  cursor: pointer;
  margin-top: -14px;
  display: flex;
  justify-content: flex-end;
  margin-left: auto;
}
.operating-conditions .operating-conditions-main .unfilled {
  padding: 15px 0 30px 0;
  border-bottom: 1px solid #ddd;
  margin-bottom: 30px;
}
.operating-conditions .operating-conditions-main .filled {
  padding: 10px 0 30px 0;
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
}
.operating-conditions .operating-conditions-main .picture-preview {
  display: flex;
  justify-content: space-between;
}
</style>

<style>
.operating-conditions-main .unfilled .input-component-item {
  height: 74px;
  width: 50%;
}
.operating-conditions-main .input-component-item strong,
.operating-conditions-main .file-upload strong {
  width: 85px;
}
.operating-conditions-main .filled .input-component-item,
.operating-conditions .operating-conditions-main .picture-preview .file-upload {
  width: 500px;
}
.el-form-item__content p{
   width: 400px;
   word-wrap: break-word;
   word-break: normal;
}
</style>



