<template>
  <div class="inspection-list-preview-preview">
    <h2 class="sub-heading">非现场检查单预览</h2>
    <div class="preview-main" id="qualificationInfo">
      <h2 class="sub-heading">资质信息</h2>
      <div class="preview-item">
        <strong class="ston">法定代表人（负责人）证件影印件:</strong>
        <img class="elimage" v-for="(file,index) in fileList" @click="privueImg(file.url)" :key="index" title="法定代表人（负责人）证件影印件（正反面)" :src="file.url"/>
      </div>
      <div class="preview-item">
        <strong class="ston inspecelimage">机构证件影印件:</strong>
        <img class="elimage" v-for="(file,index) in inspecList" @click="privueImg(file.url)"  :key="index" title="机构证件影印件" :src="file.url"/>
      </div>
    </div>
    <div class="preview-main" id="serviceProviderInfo">
      <h2 class="sub-heading">企业信息</h2>
      <div class="preview-item">
        <h3 class="sub-heading">{{companyInfo.h3Text}}</h3>
        <InputComponent :form-data="companyInfo.list" :is-input="false"></InputComponent>
      </div>
      <div class="preview-item">
        <h3 class="sub-heading">{{serviceProviderInfo.h3Text}}</h3>
        <InputComponent :form-data="serviceProviderInfo.list" :is-input="false"></InputComponent>
      </div>
      <div class="preview-item">
        <h3 class="sub-heading" style="width:100%">检查照片</h3>
<!--        <FileUpload v-for="item in inspectionPhotos.list" :key="item.title" :title="item.title" :is-preview="true" :list-type="item.valueType" :preview-url="item.value"></FileUpload>-->
        <div>
          <strong class="ston">办公现场照片</strong>
        </div>
        <img class="elimage" v-for="(file,index) in inspectionPhotosList" @click="privueImg(file.url)" :key="index" title="办公现场照片" :src="file.url" />
      </div>
<!--      <ControlButton type="ToBack" :is-disabled="false" :is-loading="isLoading" :hide-submit="hideSubmit" :save-submit="SaveSubmit"></ControlButton>-->
      <ControlButton class="btn-back" type="ConfirmSubmit" :is-disabled="false" :is-loading="isLoading" :hide-submit="hideSubmit" :save-submit="SaveSubmit"></ControlButton>
      <el-dialog id="imgRate"
                 :visible.sync="dialogVisible"
                 :show-close="false"
                 :close-on-click-modal="true"
                 width="700px" :height="height">
        <img  :src="imgSrc" alt="" width="700px" :height="height">
      </el-dialog>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import FileUpload from '@/components/FileUpload/FileUpload'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  name: 'SurveyRatingPreview',
  components: {
    InputComponent,
    FileUpload,
    ControlButton
  },
  data () {
    return {
      imgInfo: {}, // 存图片的宽高信息
      imgSrc: '',
      height: '',
      dialogVisible: false,
      fileList: [],
      inspecList: [],
      inspectionPhotosList: [],
      getInspectionUrl: this.$store.state.complianceList.getInspectionUrl,
      saveInspectionUrl: this.$store.state.complianceList.saveInspectionUrl,
      inspectionListData: {},
      hideSubmit: JSON.parse(localStorage.getItem('hideSubmit')),
      ecc_csrf_token: this.$store.state.complianceList.ecc_csrf_token,
      isLoading: false
    }
  },
  computed: {
    recordId () {
      return localStorage.getItem('previewRecordId')
    },
    companyInfo () {
      return {
        h3Text: '公司信息',
        list: [{
          title: '公司性质',
          value: this.inspectionListData.companyNature,
          valText: ['', '国有企业', '民营企业', '个体企业', '外资企业', '港澳台企业、中外合资企业', '其他'],
          valueType: 'select'
        }, {
          title: '公司实际办公地',
          value: this.inspectionListData.companyPlace,
          isRiequired: false,
          valueType: 'text'
        }, {
          title: '经营范围',
          value: this.inspectionListData.businessScope,
          isRiequired: false,
          valueType: 'text'
        }, {
          title: '法定代表人（负责人）姓名',
          value: this.inspectionListData.legalName,
          valueType: 'text'
        }, {
          title: '法定代表人（负责人）证件类型',
          value: this.inspectionListData.legalCertificateType,
          valText: ['身份证', '护照'],
          valueType: 'select'
        }, {
          title: '法定代表人（负责人）证件号码',
          value: this.inspectionListData.legalCertificateCode,
          valueType: 'text'
        }]
      }
    },
    serviceProviderInfo () {
      return {
        h3Text: '服务商信息',
        list: [{
          title: '服务商名称',
          value: this.inspectionListData.companyName,
          valueType: 'text'
        }, {
          title: '服务商证照类型',
          value: this.inspectionListData.agentCertificateType,
          valText: ['', '营业执照', '组织机构代码证'],
          valueType: 'select'
        }, {
          title: '服务商证照编号',
          value: this.inspectionListData.agentCertificateCode,
          valueType: 'text'
        }, {
          title: '服务商签约时间',
          value: this.inspectionListData.agentSignTime ? this.inspectionListData.agentSignTime.slice(0, 10) : '',
          isRiequired: true,
          valueType: 'date'
        }, {
          title: '服务商证照有效期',
          value: [this.inspectionListData.agentCertificateStartTime ? this.inspectionListData.agentCertificateStartTime.slice(0, 10) : '', this.inspectionListData.agentCertificateEndTime ? this.inspectionListData.agentCertificateEndTime.slice(0, 10) : ''],
          isRiequired: false,
          valueType: 'daterange'
        }, {
          title: '服务商证照注册地',
          value: this.inspectionListData.agentCertificatePlace,
          isRiequired: false,
          valueType: 'text'
        }]
      }
    }
  },
  created () {
    let obj = JSON.parse(localStorage.getItem('inspectionListData')) || {}
    if (obj.id !== this.recordId) {
      let params = { recordId: this.recordId }
      this.$axios.get(this.getInspectionUrl, { params: params }).then(res => {
        // console.log('请求成功', res)
        if (res && res.data && res.data.return_code === '0') {
          let list = res.data.list
          list.statusId = localStorage.getItem('statusId')
          this.inspectionListData = list
          list.legalFileListObj = this.fileList = this.inspectionListData.legalImageUrl !== '' ? JSON.parse(this.inspectionListData.legalImageUrl) : ''
          list.mechanismObj = this.inspecList = this.inspectionListData.institutionCertificateImageUrl !== '' ? JSON.parse(this.inspectionListData.institutionCertificateImageUrl) : ''
          list.fileListObj = this.inspectionPhotosList = this.inspectionListData.worksiteImageUrl !== '' ? JSON.parse(this.inspectionListData.worksiteImageUrl) : ''
          localStorage.setItem('inspectionListData', JSON.stringify(list))
          console.log(this.fileList)
        } else {
          this.$message.closeAll()
          this.$message({
            // message: res.data.return_msg,
            message: '登录超时，请刷新页面重新登录',
            type: 'error',
            // duration: 0,
            center: true
          })
        }
      }).catch(res => {
        this.$message.closeAll()
        this.$message({
          message: '请求超时,请刷新页面重试',
          type: 'error',
          // duration: 0,
          center: true
        })
      }) // .finally(() => { this.tableLoading = false })
    } else {
      this.inspectionListData = obj
      this.fileList = this.inspectionListData.legalFileListObj
      this.inspecList = this.inspectionListData.mechanismObj
      this.inspectionPhotosList = this.inspectionListData.fileListObj
    }
  },
  destroyed () {
  },
  methods: {
      privueImg (url) {
          this.imgSrc = ''
          this.height = 700
          var img = new Image()
          img.src = url
          const vm = this
          img.onload = function () {
              var heighty = img.height ? img.height : 600
              var width = img.width ? img.width : 600
              console.log('heighty:', heighty)
              console.log('width:', width)
              vm.height = 700 * (heighty / width)
              vm.imgSrc = url
          }
          this.dialogVisible = true
      },
    SaveSubmit () {
      if (this.inspectionListData.legalFileListObj === undefined) {
        this.$message.error('请上传法定代表人（负责人）证件影印件（身份证需上传正反面）')
        return
      } else if (this.inspectionListData.legalFileListObj.length === 0) {
        this.$message.error('请上传法定代表人（负责人）证件影印件（身份证需上传正反面）')
        return
      }
      if (this.inspectionListData.mechanismObj === undefined) {
        this.$message.error('请上传法定代表人（负责人）证件影印件（身份证需上传正反面）')
        return
      } else if (this.inspectionListData.mechanismObj.length !== 1) {
        this.$message.error('请上传机构证件影印件')
        return
      }
      if (this.inspectionListData.companyNature === '0') {
        this.$message.error('请选择公司性质')
        return
      }
      if (this.inspectionListData.agentCertificateType === '0') {
        this.$message.error('请选择服务商证照类型')
        return
      }
      if (this.inspectionListData.agentSignTime === '1970-01-01 00:00:01') {
        this.$message.error('请选择服务商签约时间')
        return
      }
      if (this.inspectionListData.agentCertificatePlace === '') {
        this.$message.error('请填写服务商证照注册地')
        return
      }
      if (this.inspectionListData.companyPlace === '') {
        this.$message.error('请填写公司实际办公地')
        return
      }
      this.isLoading = true
      let params = new FormData() // 创建form对象
      for (let i in this.inspectionListData) {
        // if (i !== 'mechanismObj' || i !== 'legalFileListObj') {
        params.append(i, this.inspectionListData[i])
        // }
      }
      params.append('ecc_csrf_token', this.ecc_csrf_token)
      let config = { headers: { 'Content-Type': 'multipart/form-data' } }
      this.$axios.post(this.saveInspectionUrl, params, config).then(res => {
        // console.log('请求成功', res)
        this.isLoading = false
        if (res && res.data && res.data.return_code === '0') {
          sessionStorage.setItem('submitStatus', true)
          this.$router.push('ResultSubmitted')
        } else {
          sessionStorage.setItem('submitStatus', false)
          this.$message.closeAll()
          this.$message({
            // message: res.data.return_msg,
            message: '登录超时，请刷新页面重新登录',
            type: 'error',
            // duration: 0,
            center: true
          })
        }
      }).catch(res => {
        // console.log('请求出错', res)
        this.isLoading = false
        sessionStorage.setItem('submitStatus', false)
        this.$message.closeAll()
        this.$message({
          message: '请查看数据是否填写完整,或请刷新页面重试',
          type: 'error',
          // duration: 0,
          center: true
        })
      }) // .finally(() => { this.tableLoading = false })
      //上报
      var WXPAY_JSLOG_REPORT = null;
      if(WXPAY_JSLOG_REPORT) WXPAY_JSLOG_REPORT.send({common_log:"InspectionListSubmit"}); // 提交
    }
  }
}
</script>

<style>
#imgRate .el-dialog__body,#imgRate .el-dialog__header{
  padding: 0 !important;
}
.inspection-list-preview-preview .preview-main {
  background-color: #fff;
  /* min-height: 880px; */
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  margin-bottom: 20px;
  box-shadow: #ddd 0 0 6px;
}
.inspection-list-preview-preview .preview-main h2.sub-heading {
  height: 25px;
  line-height: 25px;
}
.ston{
  font-weight: 600;
  line-height: 130px;
  margin-right: 30px;
}
.elimage{
  width: 100px;
  height: 100px;
  margin-top: 30px;
  margin-right: 30px;
}
.inspecelimage{
  margin-right: 143px;
}
/* .inspection-list-preview-preview .preview-main h3.sub-heading {
  margin: 50px 0 10px 0;
} */
.inspection-list-preview-preview .preview-main .preview-item {
  display: flex;
  flex-wrap: wrap;
  margin-top: 50px;
}
.inspection-list-preview-preview .preview-main:nth-last-child(3) {
  margin-bottom: 140px;
}
.inspection-list-preview-preview .preview-main .input-component {
  width: 100%;
  border-bottom: 1px solid #ddd;
  /* border-bottom: 1px solid #ddd; */
}
.inspection-list-preview-preview
  .preview-main:nth-last-child(3)
  .input-component {
  border: none;
}
/*#qualificationInfo .preview-item {*/
/*  border-top: 1px solid #ddd;*/
/*}*/
.inspection-list-preview-preview .preview-main .file-upload {
  width: 50%;
  padding: 20px 0;
  border-bottom: 1px solid #ddd;
}
#serviceProviderInfo .preview-item:last-child {
  margin-bottom: 75px;
}
#serviceProviderInfo .preview-item:last-child .file-upload {
  width: 100%;
}
/*.inspection-list-preview-preview .control-button {*/
.btn-back {
  width: 100%;
  height: 90px;
  background: #fff;
  /*box-shadow: 0 0 6px #ddd;*/
  /*position: fixed;*/
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px
}
</style>

<style>
.inspection-list-preview-preview
  .preview-main
  .input-component
  .input-component-item {
  width: 50%;
  padding: 20px 0;
  border-top: 1px solid #ddd;
  flex: auto;
}
.inspection-list-preview-preview
.preview-main
.input-component
.input-component-item
span{
  width: 80%;
}
.inspection-list-preview-preview .preview-main .input-component-item strong,
.inspection-list-preview-preview .preview-main .file-upload strong {
  width: 93px;
}
.inspection-list-preview-preview .preview-main .file-upload .img-preview {
  width: 100px;
  height: 100px;
}
/* .inspection-list-preview-preview .preview-main:nth-last-child(3) .input-component-item {
  align-items: flex-start;
  border: none;
  flex: none;
}
.inspection-list-preview-preview
  .preview-main:nth-last-child(3)
  .input-component-item
  span {
  color: #606266;
  line-height: 1.5;
} */
</style>



