<template>
  <div class="internal-control">
    <h2 class="sub-heading">内控水平</h2>
    <div class="internal-control-main">
      <InputComponent :form-data="radioData"></InputComponent>
      <ControlButton :next-path="nextPath" :is-disabled="!internalControlStatus" :save="Save"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  name: 'InternalControl',
  components: {
    InputComponent,
    ControlButton
  },
  data () {
    return {
      surveyRatingData: JSON.parse(localStorage.getItem('surveyRatingData')),
      keyStr: {
        isOrganizationSound: '是否有健全的组织架构',
        isInternalControlSound: '是否有健全的内控制度',
        isAntiMoneyLaundering: '是否有健全的反洗钱制度',
        isRiskManagementSound: '是否有健全的业务和风险管理制度',
        isSecuritySound: '是否有健全的信息安全和保密管理机制',
        isEmergencySound: '是否有业务运行紧急预案',
        isSupportSound: '是否有完善的服务体系和服务支持能力'
      },
      radioData: [],
      nextPath: 'CreditStatus'
    }
  },
  mounted () {
    var tempStatId = localStorage.getItem('statusId') ? localStorage.getItem('statusId') : '00'
    localStorage.setItem('internalStsa' + tempStatId, '1')
  },
  computed: {
    internalControlStatus () {
      let status = true
      for (let i in this.keyStr) {
        if (!this.surveyRatingData[i]) status = false
      }
      return status
    }
  },
  created () {
    for (let i in this.keyStr) {
      let obj = { title: this.keyStr[i], key: i, value: this.surveyRatingData[i], isRiequired: true, valueType: 'radio' }
      this.radioData.push(obj)
    }
  },
  methods: {
    Save () {
      let data = this.radioData
      for (let i in data) {
        this.surveyRatingData[data[i].key] = data[i].value
      }
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
      this.$message.closeAll()
      this.$message({
        message: '草稿保存成功',
        type: 'success',
        // duration: 0,
        center: true
      })
      return true
    }
  }
}
</script>

<style scoped>
.internal-control .internal-control-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.internal-control .internal-control-main .input-component {
  padding-bottom: 30px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
  justify-content: space-between;
}
</style>

<style>
.internal-control-main .input-component .input-component-item {
  min-height: 74px;
}
.internal-control-main .input-component-item strong {
  width: 93px;
}
</style>



