<template>
  <div class="result-submitted">
    <h2 class="sub-heading"></h2>
    <div class="result-submitted-main">
      <div class="msg-icon" :class="status ? 'success' : 'error'"></div>
      <h3>{{status ? '提交成功' : '提交驳回'}}</h3>
      <p>{{msg}}</p>
      <el-button type="success" @click="Toback">{{status ? '返回首页' : '返回修改'}}</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResultSubmitted',
  components: {
  },
  data () {
    return {
      msg: '请等待平台审核, 预计1-2日完成审核',
      nextPath: ''
    }
  },
  computed: {
    status () {
      return sessionStorage.getItem('submitStatus')
    }
  },
  methods: {
    Toback () {
      // this.$message.closeAll()
      // this.$message({
      //   message: this.status,
      //   type: 'success',
      //   // duration: 0,
      //   center: true
      // })
      // this.status ? this.$router.push('ComplianceHomepage') : this.$router.go(-1)
      // this.status ? this.$router.push('/') : this.$router.go(-1)
      this.status ? this.$router.push('/serviceProvideCheck') : this.$router.go(-1)
    }
  }
}
</script>


<style scoped>
.result-submitted .result-submitted-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.result-submitted .result-submitted-main .msg-icon {
  width: 62px;
  height: 62px;
  background: url('~@/assets/sprite2.png') no-repeat;
  margin-bottom: 25px;
}
.result-submitted .result-submitted-main .msg-icon.success {
  background-position: -133px -1px;
}
.result-submitted .result-submitted-main .msg-icon.error {
  background-position: -67px -1px;
}
.result-submitted .result-submitted-main h3 {
  font-size: 18px;
  color: #333;
  font-weight: bold;
}
.result-submitted .result-submitted-main p {
  font-size: 14px;
  color: #999;
  margin: 15px 0 25px 0;
}
</style>

<style>
.result-submitted .el-button.el-button--success {
  color: #fff;
  background-color: #00c250;
  border-color: #00c250;
}
.result-submitted .el-button.el-button--success:hover {
  background: limegreen;
  border-color: limegreen;
  color: #fff;
}
.result-submitted .el-button.el-button--success:active {
  background: #5daf34;
  border-color: #5daf34;
  color: #fff;
}
</style>



