<template>
  <div class="file-upload">
    <strong :class="{'riequired': isRiequired}">{{title}}</strong>
    <img v-if="isPreview" class="img-preview" :src="previewUrl" alt="">
    <el-upload v-else :style="listType == 'picture-card' ? 'height: 128px;' : ''" :class="{'disabled': has}" :list-type="listType" multiple :limit="fileLimit" ref="upload" :auto-upload="true" :action="actionUrl" :file-list="fileList" :on-change="handleChange" :http-request="uploadFile" :before-upload="beforeAvatarUpload" :on-success="handleAvatarSuccess" :on-error="handleAvatarError" :on-preview="handlePreview" :before-remove="beforeRemove" :on-remove="handleRemove">
      <i v-if="listType == 'picture-card'" class="el-icon-plus"></i>
      <el-button v-else type="info" plain><i class="el-icon-upload"></i>上传文件</el-button>
      <div slot="tip" class="el-upload__tip" v-if="tipText !== 'none'">{{tipText}}</div>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'FileUpload',
  props: {
    name: {
      // type: String,
      default: ''
    },
    previewUrl: {
      type: String,
      default: ''
    },
    title: {
      required: true,
      type: String
    },
    isRiequired: {
      default: false
    },
    fileLimit: {
      type: Number,
      default: 3
    },
    isPreview: {
      type: Boolean,
      default: false
    },
    actionUrl: {
      default: 'https://pay.weixin.qq.com/index.php/xphp/ccagentcheck/uploadImage?ishttp=1'
    },
    listType: {
      default: 'file'
    },
    fileListProp: {
      type: Array,
      default () {
        return []
      }
    },
    handleAvatarSuccess: {
      type: Function
    },
    tipText: {
      default: '图片建议尺寸:128X128像素,大小不超过1M,支持JPG、PNG格式'
    }
  },
  data () {
    return {
      ecc_csrf_token: this.$store.state.complianceList.ecc_csrf_token,
      has: false,
      // has: true,
      dialogImageUrl: '',
      dialogVisible: false
    }
  },
  created () {
    this.has = Boolean(this.fileListProp.length)
    this.fileList = JSON.parse(JSON.stringify(this.fileListProp))
  },
  computed: {
  },
  watch: {
    isPreview: {
      handler (val, old) {
        this.fileList = JSON.parse(JSON.stringify(this.fileListProp))
      }
    },
    fileListProp: {
      handler (val, old) {
        // this.fileList = JSON.parse(JSON.stringify(this.fileListProp));
        this.has = Boolean(this.fileListProp.length)
      }
    }
  },
  mounted () {
    this.has = Boolean(this.fileListProp.length)
  },
  methods: {
    handleChange (file, fileList) {
      // this.has = Boolean(this.fileList.length)
      // this.fileList = fileList
    },
    beforeAvatarUpload (file) {
      if (
        this.listType === 'picture-card' &&
        file.type !== 'image/jpg' &&
        file.type !== 'image/gif' &&
        file.type !== 'image/png' &&
        file.type !== 'image/jpeg'
      ) {
        this.$message({ type: 'error', message: '支持jpg, jpeg, png, gif格式图片上传' })
        return false
      } else if (
        this.listType !== 'picture-card' &&
        file.type !== 'application/vnd.openxmlformats-officedocument.wordprocessingml.document' && // docx
        file.type !== 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' && // xlsx
        file.type !== 'application/pdf' &&
        file.type !== 'text/plain' &&
        file.type !== 'application/vnd.ms-excel' // .xls
      ) {
        this.$message({ type: 'error', message: '支持word, excel, pdf, txt格式文件上传' })
        return false
      }
      if (file.size > 2 * 1024 * 1024) {
        this.$message({ type: 'error', message: '上传的文件大小需在2M以内' })
        return false
      } else if (file.size === 0) {
        this.$message({ type: 'error', message: '请不要上传空文件' })
        return false
      }
      // if (this.fileLists.indexOf(file.name) >= 0) {
      //   this.$message({ type: 'error', message: '图片已经上传' })
      //   return false
      // }
      return true
    },
    uploadFile: function (file) {
      // this.has = true
      var param = new FormData() // 创建form对象
      param.append('img', file.file)
      param.append('ecc_csrf_token', this.ecc_csrf_token)
      // 添加请求头
      var config = { headers: { 'Content-Type': 'multipart/form-data' } }
      this.$axios.post(file.action, param, config).then(res => {
        if (res && parseInt(res.data.return_code) !== 0) {
          this.$message.error(res.data.return_msg)
          this.has = false
          file.onError()
        } else {
          file.onSuccess(res.data)
        }
      }).catch(() => {
        this.$message({
          message: '请求超时,请刷新页面重试',
          type: 'error',
          // duration: 0,
          center: true
        })
        this.has = false
        file.onError()
      })
    },
    // handleAvatarSuccess (res, file, fileList) {
    // },
    handleAvatarError (res, file) {
      // console.log('上传失败', res, file)
    },
    handlePreview (file) {
      // console.log('handlePreview', file)
      if (this.listType === 'picture-card') {
        this.dialogImageUrl = file.url
        this.dialogVisible = true
      } else {
        window.location.href = file.url ? file.url : file.response.furl
      }
    },
    beforeRemove (file, fileList) {
      // console.log(file, fileList, this.fileList)
      // return this.$confirm(`确定移除 ${file.name}？`)
    },
    handleRemove (file, fileList) {
      this.fileList = fileList
      this.has = false
      this.$emit('fileRemove', this.name, fileList)
    }
  }
}
</script>

<style>
.el-upload-list__item-name{
  width:300px
}
.file-upload {
  min-height: 141px;
  line-height: 20px;
  display: flex;
  box-sizing: border-box;
  padding-bottom: 14px;
}
.file-upload strong {
  width: 79px;
  flex: none;
  display: flex;
  margin-right: 20px;
  line-height: 1.4;
  font-weight: 600;
}
.file-upload strong.riequired:after {
  content: '*';
  color: red;
  flex: none;
  margin-left: 3px;
}
.file-upload img.img-preview {
  width: 128px;
  height: 128px;
  vertical-align: top;
  border-radius: 4px;
}
.file-upload .disabled {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.file-upload .disabled .el-upload--picture-card {
  display: none !important;
}
.file-upload .disabled .el-upload-list--picture-card .el-upload-list__item {
  width: 128px;
  height: 128px;
}
.file-upload .el-dialog {
  width: max-content;
  margin-top: 5vh !important;
}
.file-upload .el-upload__tip {
  max-width: 337px;
  line-height: 20px;
  margin: 14px 0 10px 0;
}
.file-upload .disabled .el-upload__tip {
  display: none !important;
}
/* 上传文件 按钮 */
.file-upload .el-button {
  width: 120px;
}
.file-upload .disabled .el-button {
  display: none !important;
}
.file-upload .el-button--info.is-plain {
  color: #555;
  border: none;
  background: #eee;
}
/* .file-upload .el-button--info.is-plain:focus, */
.file-upload .el-button--info.is-plain:hover {
  background: #909399;
  color: #fff;
}
</style>



