import util from '../../units/util'
const state = {
  // 查询服务商合规记录接口
  complianceUrl: 'https://pay.weixin.qq.com/index.php/xphp/ccagentcheck/getServiceProvide?ishttp=1',
  // 查询服务商合规巡检记录接口
  getInspectionUrl: 'https://pay.weixin.qq.com/index.php/xphp/ccagentcheck/getCheckAgentInspection?ishttp=1',
  // 保存服务商合规巡检记录接口
  saveInspectionUrl: 'https://pay.weixin.qq.com/index.php/xphp/ccagentcheck/saveServiceProvide?ishttp=1',
  // 图片文件上传接口
  fileUploadUrl: 'https://pay.weixin.qq.com/index.php/xphp/ccagentcheck/uploadImage?ishttp=1',
  // 查询服务商合规尽职调查记录接口
  getSurveyRatingUrl: 'https://pay.weixin.qq.com/index.php/xphp/ccagentcheck/getCheckAgentDuty?ishttp=1',
  // 保存服务商合规记录尽职调查记录接口
  saveSurveyRatingUrl: 'https://pay.weixin.qq.com/index.php/xphp/ccagentcheck/saveCheckAgentDuty?ishttp=1',
  // token
  ecc_csrf_token: process.env.NODE_ENV === 'production' ? util.getCookie('ecc_csrf_cookie') : '11ab73856a7a72a1e60e406dab2f4342',
  // 当前操作的记录ID recordId
  previewRecordId: localStorage.getItem('previewRecordId')
}
const getters = {
  // 当前操作的记录ID
  // previewRecordId: state => state.previewRecordId
}
const mutations = {
  setPreviewRecordId (state, previewRecordId) {
    state.previewRecordId = previewRecordId
  }
}
const actions = {
  savePreviewRecordId ({ state, commit }, obj) {
    commit('setPreviewRecordId', obj)
  }
}
export default {
  state,
  getters,
  mutations,
  actions
}



