<template>
  <div class="risk-control-ability">
    <h2 class="sub-heading">风控能力</h2>
    <div class="risk-control-ability-main">
      <InputComponent :form-data="riskControlData"></InputComponent>
      <InputComponent v-show="riskControlData[5].value === '1'" :form-data="riskEventRadio"></InputComponent>
      <el-form :model="riskEventForm" v-show="riskControlData[5].value === '1'" :rules="formRules" ref="riskEventForm" label-width="100px" label-position="left" class="risk-form">
        <el-form-item v-for="(item, i) in riskEventData" :label="item.title" :prop="item.key" :key="i">
          <el-input v-if="item.valueType == 'number'" type="number" v-model="riskEventForm[item.key]"></el-input>
          <el-input v-else v-model="riskEventForm[item.key]"></el-input>
        </el-form-item>
      </el-form>
      <ControlButton :next-path="nextPath" :is-disabled="!RiskControlAbilityStatus" :save="Save"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import ControlButton from '@/components/ControlButton/ControlButton'
import { isPositiveInteger, validatetoFixedNew, isBtnZeroToHundred } from '@/units/validator'

export default {
  name: 'RiskControlAbility',
  components: {
    InputComponent,
    ControlButton
  },
  data () {
    return {
      surveyRatingData: JSON.parse(localStorage.getItem('surveyRatingData')),
      controlKey: {
        isPlanElmergencyDrill: '是否制定年度应急演练计划',
        isLaunchElmergencyDrill: '是否按计划开展应急演练',
        isEmergencyHandle: '是否建立完善的突发事件处理制度',
        isCheckMch: '是否对商户进行巡检',
        isTrainMch: '是否对商户进行培训',
        isRiskHappened: '是否发生过风险事件'
      },
      eventKey: {
        riskHappenedNumber: '发生风险事件数量',
        // isMchRiskHappened: '拓展商户是否发生风险事件',
        swindleMoney: '涉及欺诈交易金额(万元)',
        swindleRatio: '收单欺诈率(%)'
      },
      riskControlData: [],
      riskEventRadio: [{
        title: '拓展商户是否发生风险事件',
        key: 'isMchRiskHappened',
        value: '',
        isRiequired: true,
        valueType: 'radio'
      }],
      riskEventData: [],
      riskEventForm: {},
      formRules: {
        riskHappenedNumber: [
          { required: true, message: '请输入发生风险事件数量', trigger: 'blur' },
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        swindleMoney: [
          { required: true, message: '请输入涉及欺诈交易金额(万元)', trigger: 'blur' },
          { validator: validatetoFixedNew, trigger: 'blur' }
        ],
        swindleRatio: [
          { required: true, message: '请输入收单欺诈率(%)', trigger: 'blur' },
          { validator: isBtnZeroToHundred, trigger: 'blur' }
        ]
      },
      nextPath: 'OtherComments'
    }
  },
  computed: {
    RiskControlAbilityStatus () {
      let status = true
      if (this.riskControlData[5].value === '1') {
        for (let i in this.eventKey) {
          if (!this.surveyRatingData[i]) status = false
        }
      }
      return status
    }
  },
  watch: {
    riskControlData: {
      handler (val, oldval) {
        for (let i in val) {
          this.surveyRatingData[val[i].key] = val[i].value
        }
      },
      immediate: false,
      deep: true
    },
    riskEventRadio: {
      handler (val, oldval) {
        for (let i in val) {
          this.surveyRatingData[val[i].key] = val[i].value
        }
      },
      immediate: false,
      deep: true
    },
    riskEventForm: {
      handler (val, oldval) {
        for (let i in val) {
          this.surveyRatingData[i] = val[i]
        }
      },
      immediate: false,
      deep: true
    }
  },
  created () {
    this.riskEventRadio[0].value = this.surveyRatingData.isMchRiskHappened
    for (let i in this.controlKey) {
      let obj = { title: this.controlKey[i], key: i, value: this.surveyRatingData[i], isRiequired: true, valueType: 'radio' }
      this.riskControlData.push(obj)
    }
    for (let i in this.eventKey) {
      let obj = { title: this.eventKey[i], key: i, value: this.surveyRatingData[i], isRiequired: true, valueType: 'number' }
      this.riskEventData.push(obj)
    }
    let riskEventForm = {}
    for (let i in this.riskEventData) {
      riskEventForm[this.riskEventData[i].key] = this.riskEventData[i].value
    }
    this.riskEventForm = riskEventForm
    // console.log(this.riskEventRadio, this.riskControlData, this.riskEventData, this.riskEventForm)
  },
  mounted () {
    var tempStatId = localStorage.getItem('statusId') ? localStorage.getItem('statusId') : '00'
    localStorage.setItem('RiskControlStsa' + tempStatId, '1')
  },
  methods: {
    Save () {
      let formStatus = true
      this.riskControlData[5].value === '1' && this.$refs['riskEventForm'].validate((valid) => {
        if (!valid) {
          if (this.surveyRatingData.swindleRatio > 100) {
            this.surveyRatingData.swindleRatio = ''
          }
        }
      })
      if (formStatus) {
        localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
        this.$message.closeAll()
        this.$message({
          message: '草稿保存成功',
          type: 'success',
          // duration: 0,
          center: true
        })
        return true
      }
      return false
    }
  }
}
</script>

<style scoped>
.risk-control-ability .risk-control-ability-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.risk-control-ability .risk-control-ability-main .input-component {
  justify-content: space-between;
}
.risk-control-ability .risk-control-ability-main .input-component:nth-child(2) {
  /* margin-left: calc(100% - 500px); */
}
.risk-control-ability .risk-control-ability-main .control-button {
  padding-top: 40px;
  border-top: 1px solid #ddd;
  margin-top: 30px;
}
.risk-control-ability .risk-control-ability-main .risk-form {
  /* margin-top: -56px; */
}
.risk-control-ability
  .risk-control-ability-main
  .risk-form
  .el-form-item:first-child {
  /* margin-right: calc(100% - 500px); */
  
}

.risk-control-ability-main .el-form--label-left{
  margin-top:15px;
}

</style>

<style>

.risk-control-ability-main .input-component .input-component-item {
  min-height: 74px;
 
}
.risk-control-ability-main .input-component-item strong {
  width: 93px;
}
</style>



