<template>
  <div class="result-submitted">
    <h2 class="sub-heading"></h2>
    <div class="result-submitted-main">
      <div class="msg-icon"></div>
      <h3>提交驳回</h3>
      <p class="audiomsg">驳回原因：{{msg}}</p>
      <el-button type="success" @click="Toback">返回修改</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResultAudit',
  components: {
  },
  data () {
    return {
      msg: localStorage.getItem('FRejectReason'),
      nextPath: ''
    }
  },
  computed: {
    status () {
      return sessionStorage.getItem('submitStatus')
    }
  },
  methods: {
    Toback () {
      // this.$message.closeAll()
      // this.$message({
      //   message: this.status,
      //   type: 'success',
      //   // duration: 0,
      //   center: true
      // })
      // this.status ? this.$router.push('ComplianceHomepage') : this.$router.go(-1)
      let type = localStorage.getItem('pushType')
      let url = type === '1' ? 'InspectionList' : 'SurveyRating'
      this.$router.push(url)
      // this.status ?  : this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
.result-submitted .result-submitted-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.result-submitted .result-submitted-main .msg-icon {
  width: 65px;
  height: 65px;
  background: url('~@/assets/sprite3.png') no-repeat;
  margin-bottom: 25px;
}
.result-submitted-main{
  padding-left: 100px!important;
  padding-right: 100px!important;
}

/*.result-submitted .result-submitted-main .msg-icon.success {*/
/*  background-position: -133px -1px;*/
/*}*/
.audiomsg{
  color: red!important;
}
/*.result-submitted .result-submitted-main .msg-icon.error {*/
/*  background-position: -67px -1px;*/
/*}*/
.result-submitted .result-submitted-main h3 {
  font-size: 18px;
  color: #333;
  font-weight: bold;
}
.result-submitted .result-submitted-main p {
  font-size: 14px;
  color: #999;
  margin: 15px 0 25px 0;
}
</style>

<style>
.result-submitted .el-button.el-button--success {
  color: #fff;
  background-color: #00c250;
  border-color: #00c250;
}
.result-submitted .el-button.el-button--success:hover {
  background: limegreen;
  border-color: limegreen;
  color: #fff;
}
.result-submitted .el-button.el-button--success:active {
  background: #5daf34;
  border-color: #5daf34;
  color: #fff;
}
</style>



