/* 是否身份证号码 */
export function validateIdTwoNo(value, msg1, msg2, callback) {
  const reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
  if (value === '') {
    callback(new Error(msg1))
  } else {
    if ((!reg.test(value)) && value !== '') {
      callback(new Error(msg2))
    } else {
      callback()
    }
  }
}
/* 是否身份证号码 */
export function validateIdNo(rule, value, callback) {
  const reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
  if (value === '') {
    callback(new Error('请填写正确的身份证号码'))
  } else {
    if ((!reg.test(value)) && value !== '') {
      callback(new Error('请填写正确的身份证号码'))
    } else {
      callback()
    }
  }
}

/* 是否手机号码或者固话 */
export function validatePhoneTwo(value, msg1, msg2, callback) {
  const reg = /^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/
  if (value === '') {
    callback(new Error(msg1))
  } else {
    if ((!reg.test(value)) && value !== '') {
      callback(new Error(msg2))
    } else {
      callback()
    }
  }
}
/* 是否手机号码 */
export function validatePhone(rule, value, callback) {
  const reg = /^[1][3,4,5,7,8][0-9]{9}$/
  if (value === '' || value === undefined || value == null) {
    callback()
  } else {
    if ((!reg.test(value)) && value !== '') {
      callback(new Error('请输入正确的电话号码'))
    } else {
      callback()
    }
  }
}
/* 是否固话 */
export function validateTelphone(rule, value, callback) {
  const reg = /0\d{2}-\d{7,8}/
  if (value === '' || value === undefined || value == null) {
    callback()
  } else {
    if ((!reg.test(value)) && value !== '') {
      callback(new Error('请输入正确的固话（格式：区号+号码,如010-1234567）'))
    } else {
      callback()
    }
  }
}

/* 金额 正数保留2位小数 */
export function validatetoFixedNew(rule, value, callback) {
  const reg = /^(([1-9]{1}\d*)|([0]{1}))(\.(\d){1,2})?$/
  if (value === '') {
    callback()
  } else {
    if (value.indexOf('.') != -1 && value.length > 17) {
      callback(new Error('长度不能超过16位'))
    } else if (value.indexOf('.') == -1 && value.length > 16) {
      callback(new Error('长度不能超过16位'))
    }
    if (!reg.test(value)) {
      callback(new Error('请输入长度不能超过16位的数字，并最多保留两位小数'))
    } else {
      // var num = value.split('.').length - 1
      if (value.split('.').length > 2) {
        callback(new Error('请输入正确格式的数据'))
      } else {
        callback()
      }
      callback()
    }
  }
}

