// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import Router from 'vue-router'
import router from './router'
import 'babel-polyfill' // ie11

import ekyrinUI from '@tencent/ekyrin-ui'
import ekyrinLocale from '@tencent/ekyrin-ui/lib/locale/lang/zh-CN'
// import ElementUI from 'element-ui' // element-ui的全部组件
// import i18n from 'vue-i18n'
import 'babel-polyfill'
import store from './store'
// import axios from 'axios'
// import VueAxios from 'vue-axios'
import axiosconfig from './units/axiosconfig'
import util from './units/util'

import '@tencent/ekyrin-ui/lib/theme/theme-chalk/index.css'
// import 'element-ui/lib/theme-chalk/index.css'// element-ui的css
import '@/common/reset.css'
import '@/common/common.css'

// Vue.use(i18n)

// const locale = location.pathname.split('/').slice(-1)[0].split('_')[1] || 'zh'

// const Vue18n = new i18n({
//   locale,    // 语言标识
//   messages: {
//     'zh': require('./units/lang/cn'),   // 中文语言包
//     'en': require('./units/lang/en')    // 英文语言包
//   }
// })
Vue.use(Router)
Vue.use(ekyrinUI, { ekyrinLocale })
// Vue.use(ElementUI)
// Vue.use($mmpay)
// Vue.use(axios)
// Vue.use(VueAxios, axios)
Vue.prototype.$axios = axiosconfig()
Vue.prototype.$UTIL = util
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  // i18n: Vue18n,
  router,
  store,
  components: { App },
  template: '<App/>'
})



