<template>
  <div class="enterprise-info">
    <h2 class="sub-heading">企业信息</h2>
    <div class="enterprise-main">
      <h3 class="sub-heading" v-if="unfillData.length">待填项</h3>
      <el-form :model="unfillForm" :rules="formRules" ref="unfillForm" label-width="100px" label-position="left" class="unfill-form">
        <el-form-item v-for="(item, i) in unfillData" :label="item.title" :prop="item.key" :key="i">
          <el-select v-if="item.valueType == 'select'" v-model="unfillForm[item.key]">
            <el-option v-for="(itemj, j) in item.options" :label="itemj.text" :value="itemj.value" :key="j"></el-option>
          </el-select>
          <el-date-picker v-else-if="item.valueType == 'date'" type="date" placeholder="选择日期" v-model="unfillForm[item.key]" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
          <el-date-picker v-else-if="item.valueType === 'daterange'" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" v-model="unfillForm[item.key]" range-separator="-" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
          <el-input v-else v-model="unfillForm[item.key]" :placeholder="item.placehoder"></el-input>
        </el-form-item>
      </el-form>
      <h3 class="sub-heading">已填项</h3>
      <div class="input-enable" @click="InputEnable">{{isInput ? "取消修改" : "修改"}}</div>
      <div class="filled">
        <el-form :model="fillForm" :rules="formRules" ref="fillForm" label-width="100px" label-position="left" class="fill-form">
          <el-form-item v-for="(item, i) in fillData" :label="item.title" :prop="item.key" :key="i">
            <p v-if="!isInput">{{valueText(item, fillForm[item.key])}}</p>
            <el-select v-else-if="item.valueType == 'select'" v-model="fillForm[item.key]">
              <el-option v-for="(itemj, j) in item.options" :label="itemj.text" :value="itemj.value" :key="j"></el-option>
            </el-select>
            <el-date-picker v-else-if="item.valueType == 'date'" type="date" placeholder="选择日期" v-model="fillForm[item.key]" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            <el-date-picker v-else-if="item.valueType === 'daterange'" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" v-model="fillForm[item.key]" range-separator="-" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            <el-input v-else v-model="fillForm[item.key]"></el-input>
          </el-form-item>
        </el-form>
        <!-- <InputComponent :form-data="fillData" :is-input="isInput"></InputComponent> -->
      </div>
      <ControlButton :next-path="nextPath" :save="Save" :is-disabled="!enterpriseInfoStatus"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  components: {
    InputComponent,
    ControlButton
  },
  data () {
    return {
      inspectionListData: JSON.parse(localStorage.getItem('inspectionListData')),
      keyStr: {
        // id: '记录ID',
        companyName: '服务商名称',
        companyNature: '公司性质',
        agentCertificateType: '服务商证照类型',
        agentCertificateCode: '服务商证照编号',
        // institutionCertificateImage: '机构证照影印件',
        legalName: '法定代表人（负责人）姓名',
        legalCertificateType: '法定代表人（负责人）证件类型',
        legalCertificateCode: '法定代表人（负责人）证件号码',
        // legalImage: '法定代表人影印件',
        agentSignTime: '服务商签约时间',
        agentCertificateStartTime: '服务商证照有效期(经营起始时间)',
        // agentCertificateEndTime: '服务商证照有效期(经营失效时间)',
        agentCertificatePlace: '服务商证照注册地',
        companyPlace: '公司实际办公地',
        businessScope: '经营范围'
        // worksiteImage: '办公现场检查照片'
      },
      unfillData: [],
      unfillForm: {},
      fillData: [],
      fillForm: {},
      formRules: {
        companyName: [
          { required: true, message: '请输入服务商名称', trigger: 'blur' }
          // { min: 2, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        companyNature: [
          { required: true, message: '请选择公司性质', trigger: 'change' }
        ],
        agentCertificateType: [
          { required: true, message: '请选择服务商证照类型', trigger: 'change' }
        ],
        agentCertificateCode: [
          { required: true, message: '请输入服务商证照编号', trigger: 'blur' }
        ],
        legalName: [
          { required: true, message: '请输入法定代表人（负责人）姓名', trigger: 'blur' }
        ],
        legalCertificateType: [
          { required: true, message: '请选择法定代表人（负责人）证件类型', trigger: 'change' }
        ],
        legalCertificateCode: [
          { required: true, message: '请输入法定代表人（负责人）证件号码', trigger: 'blur' }
        ],
        agentSignTime: [
          { required: true, message: '请选择日期', trigger: 'blur' },
          { required: true, message: '请选择日期', trigger: 'change' }
        ],
        agentCertificateStartTime: [
          { required: true, message: '请选择日期', trigger: 'blur' },
          { required: true, message: '请选择日期', trigger: 'change' }
        ],
        agentCertificatePlace: [
          { required: true, message: '请输入服务商证照注册地', trigger: 'blur' }
        ],
        companyPlace: [
          { required: true, message: '请输入公司实际办公地', trigger: 'blur' }
        ],
        businessScope: [
          { required: false, message: '请输入经营范围', trigger: 'blur' }
        ]
      },
      isInput: false,
      initFillForm: '',
      nextPath: 'InspectionPhotos'
    }
  },
  computed: {
    enterpriseInfoStatus () {
      let status = true
      for (let i in this.keyStr) {
        if (!this.inspectionListData[i] && i !== 'businessScope') status = false
      }
      return status
    }
  },
  watch: {
    unfillForm: {
      handler (val, oldval) {
        this.Update(val)
      },
      immediate: false,
      deep: true
    },
    fillForm: {
      handler (val, oldval) {
        this.Update(val)
      },
      immediate: false,
      deep: true
    }
  },
  created () {
    for (let i in this.keyStr) {
      let obj = { title: this.keyStr[i], value: this.inspectionListData[i], key: i, isRiequired: true, valueType: 'text' }
      if (i === 'businessScope') obj.isRiequired = false
      switch (i) {
        case 'companyNature': // 公司性质
          obj.valText = ['', '国有企业', '民营企业', '个体企业', '外资企业', '港澳台企业、中外合资企业', '其他']
          obj.value === '0' && (obj.value = '')
          obj.options = [{ text: '国有企业', value: '1' }, { text: '民营企业', value: '2' }, { text: '个体企业', value: '3' }, { text: '外资企业', value: '4' }, { text: '港澳台企业、中外合资企业', value: '5' }, { text: '其他', value: '6' }]
          obj.valueType = 'select'
          break
        case 'agentCertificateType': // 服务商证照类型
          obj.valText = ['', '营业执照']
          obj.value === '0' && (obj.value = '')
          obj.options = [{ text: '营业执照', value: '1' }]
          obj.valueType = 'select'
          break
        case 'legalCertificateType': // 法定代表人证件类型
          obj.valText = ['身份证', '护照']
          obj.options = [{ text: '身份证', value: '0' }, { text: '护照', value: '1' }]
          obj.valueType = 'select'
          break
        case 'agentSignTime': // 服务商签约时间
          if (obj.value === '1970-01-01 00:00:01') { obj.value = '' } else { obj.value = obj.value.slice(0, 10) }
          obj.valueType = 'date'
          break
        case 'agentCertificateStartTime': // 服务商证照有效期(经营起始时间)
          obj.title = '服务商证照有效期'
          if (this.inspectionListData.agentCertificateStartTime === '1970-01-01 00:00:01' || this.inspectionListData.agentCertificateStartTime === '' || this.inspectionListData.agentCertificateEndTime === '1970-01-01 00:00:01' || this.inspectionListData.agentCertificateEndTime === '') {
            obj.value = []
          } else {
            obj.value = [this.inspectionListData.agentCertificateStartTime.slice(0, 10), this.inspectionListData.agentCertificateEndTime.slice(0, 10)]
          }
          obj.valueType = 'daterange'
          break
        case 'agentCertificatePlace': case 'companyPlace': // 服务商证照类型
          obj.placehoder = '例如：广东省深圳市南山区海天2路33号'
          break
        default:
          break
      }
      obj.value ? this.fillData.push(obj) : this.unfillData.push(obj)
    }

    let unfillForm = {}
    let fillForm = {}
    for (let i in this.unfillData) {
      unfillForm[this.unfillData[i].key] = this.unfillData[i].value
    }
    for (let i in this.fillData) {
      fillForm[this.fillData[i].key] = this.fillData[i].value
    }
    this.unfillForm = unfillForm
    this.fillForm = fillForm
  },
  methods: {
    valueText (item, value) {
      let text = Array.isArray(value) ? value[0] + ' - ' + value[1] : item.valueType === 'select' ? item.valText[value] : item.valueType === 'radio' ? (value === '1' ? '是' : '否') : value
      return text
    },
    InputEnable () {
      if (!this.isInput) {
        this.initFillForm = JSON.stringify(this.fillForm)
      } else {
        this.fillForm = JSON.parse(this.initFillForm)
        let _this = this
        setTimeout(() => {
          _this.$refs['fillForm'].validate((valid) => {
            if (!valid) {
              return false
            }
          })
        }, 50)
      }
      this.isInput = !this.isInput
    },
    Save () {
      this.isInput = false
      localStorage.setItem('inspectionListData', JSON.stringify(this.inspectionListData))
      this.$message.closeAll()
      this.$message({
        message: '草稿保存成功',
        type: 'success',
        // duration: 0,
        center: true
      })
      // return false
    },
    Update (data) {
      for (let i in data) {
        // console.log(i, data[i])
        if (i === 'agentCertificateStartTime') {
          this.inspectionListData.agentCertificateStartTime = data[i] && data[i][0] ? data[i][0] + ' 00:00:01' : ''
          this.inspectionListData.agentCertificateEndTime = data[i] && data[i][1] ? data[i][1] + ' 00:00:01' : ''
        } else if (i === 'agentSignTime') {
          this.inspectionListData.agentSignTime = data[i] ? data[i] + ' 00:00:01' : ''
        } else {
          this.inspectionListData[i] = data[i]
        }
      }
      // console.log(this.inspectionListData)
    }
  }
}
</script>

<style scoped>
.enterprise-info {
}
.enterprise-info .enterprise-main {
  background-color: #fff;
  min-height: 780px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.enterprise-info .enterprise-main .input-enable {
  width: max-content;
  color: cornflowerblue;
  cursor: pointer;
  margin-top: -14px;
  display: flex;
  justify-content: flex-end;
  margin-left: auto;
}
.enterprise-info .enterprise-main .unfilled {
  padding: 15px 0 30px 0;
  border-bottom: 1px solid #ddd;
  margin-bottom: 30px;
}
.enterprise-info .enterprise-main .filled {
  padding: 10px 0 30px 0;
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
}
.enterprise-info .enterprise-main .input-component {
  justify-content: space-between;
}
</style>



