<template>
  <div class="other-comments">
    <h2 class="sub-heading">其他</h2>
    <div class="other-comments-main">
      <InputComponent :form-data="commentsData"></InputComponent>
      <ControlButton type="SaveToback" :next-path="nextPath" :is-disabled="false" :save-toback="SaveToback" :save-submit="SaveSubmit"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  name: 'OtherComments',
  components: {
    InputComponent,
    ControlButton
  },
  data () {
    return {
      surveyRatingData: JSON.parse(localStorage.getItem('surveyRatingData')),
      commentsData: [{
        title: '服务商意见',
        value: '',
        isRiequired: false,
        valueType: 'textarea'
      }],
      nextPath: 'SurveyRating',
      previewPath: 'SurveyRatingPreview'
    }
  },
  created () {
    this.commentsData[0].value = this.surveyRatingData.agentSuggestion
  },
  watch: {
    commentsData: {
      handler (val, oldval) {
        this.surveyRatingData.agentSuggestion = this.commentsData[0].value
      },
      immediate: false,
      deep: true
    }
  },
  methods: {
    SaveToback () {
      this.commentsData[0].value = this.surveyRatingData.agentSuggestion
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
      this.$message.closeAll()
      this.$message({
        message: '草稿保存成功',
        type: 'success',
        // duration: 0,
        center: true
      })
      this.$router.push(this.nextPath)
    },
    SaveSubmit () {
      this.surveyRatingData.agentSuggestion = this.commentsData[0].value
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
      this.$router.push(this.previewPath)
    }
  }
}
</script>

<style scoped>
.other-comments .other-comments-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.other-comments .other-comments-main .input-component {
  padding-bottom: 30px;
  margin-bottom: 40px;
}
</style>

<style>
.other-comments-main .input-component .input-component-item {
  align-items: flex-start;
}
.other-comments-main .input-component .input-component-item strong {
  margin-top: 10px;
}
</style>



