<template>
  <div class="management-team">
    <h2 class="sub-heading">管理团队</h2>
    <div class="management-team-main">
      <div class="unfilled-item" v-for="(item, i) in unfilledData" :key="i">
        <div class="operat">
          <i class="el-icon-circle-plus-outline" v-if="unfilledData.length < 5" @click="addMemmber"></i>
          <i class="el-icon-remove-outline" style="margin-left: 30px" v-if="unfilledData.length > 1" @click="removeMemmber(i)"></i>
        </div>
        <InputComponent :form-data="item.inputData"></InputComponent>
        <FileUpload title="高级管理人员职务" :file-limit="1" :tip-text="tipText" :file-list-prop="item.fileList" :name="i" :handle-avatar-success="(res, file, fileList)=> uploadSuccess(i, res, file, fileList)" @fileRemove="(res, file, fileList)=> fileRemove(i, res, file, fileList)"></FileUpload>
      </div>
      <ControlButton :next-path="nextPath" :is-disabled="!managementTeamStatus" :save="Save"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import FileUpload from '@/components/FileUpload/FileUpload'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  name: 'ManagementTeam',
  components: {
    InputComponent,
    FileUpload,
    ControlButton
  },
  data () {
    return {
      surveyRatingData: JSON.parse(localStorage.getItem('surveyRatingData')),
      // recordId: localStorage.getItem('previewRecordId'),
      keyStr: {
        teamId: '管理团队ID',
        dutyId: '尽职调查ID',
        post: '高级管理人员职务',
        name: '姓名',
        department: '部门',
        responsibility: '主要职责'
      },
      teamData: [{
        id: '',
        dutyId: '',
        post: '',
        name: '',
        department: '',
        responsibility: ''
      }],
      unfilledData: [
        {
          inputData: [{
            title: '姓名',
            value: ''
          }, {
            title: '部门',
            value: ''
          }, {
            title: '主要职责',
            value: ''
          }],
          fileList: []
        }
      ],
      tipText: '请上传该高级管理人员职务文件,建议使用word、excel、pdf等,文件不大于2MB',
      nextPath: 'FinancialStatus'
    }
  },
  computed: {
    managementTeamStatus () {
      return true
      // return Boolean(this.surveyRatingData.checkAgentDutyTeam)
    }
  },
  created () {
    // 返回的是大写 checkAgentDutyTeam
    if (this.surveyRatingData.checkAgentDutyTeam) {
      this.teamData = JSON.parse(this.surveyRatingData.checkAgentDutyTeam)
      let arr = []
      for (let i in this.teamData) {
        var obj = {
          inputData: [{
            title: '姓名',
            value: this.teamData[i].name
          }, {
            title: '部门',
            value: this.teamData[i].department
          }, {
            title: '主要职责',
            value: this.teamData[i].responsibility
          }],
          fileList: this.teamData[i].post ? [{ name: this.teamData[i].post ? this.teamData[i].post : '', url: this.teamData[i].postUrl ? this.teamData[i].postUrl : '' }] : []
        }
        arr.push(obj)
      }
      this.unfilledData = arr
      // console.log(this.teamData, this.unfilledData)
    } else {
      this.teamData[0].dutyId = this.surveyRatingData.recordId
    }
  },
  methods: {
    addMemmber () {
      let newItem = {
        inputData: [{
          title: '姓名',
          value: ''
        }, {
          title: '部门',
          value: ''
        }, {
          title: '主要职责',
          value: ''
        }],
        fileList: []
      }
      this.unfilledData.push(newItem)
      let newObj = {
        id: '',
        dutyId: this.surveyRatingData.recordId,
        post: '',
        name: '',
        department: '',
        responsibility: ''
      }
      this.teamData.push(newObj)
    },
    removeMemmber (i) {
      this.unfilledData.splice(i, 1)
      this.teamData.splice(i, 1)
    },
    uploadSuccess (i, res, file, fileList) {
      if (res.return_code === '0') {
        this.teamData[i].post = res.fname
        this.teamData[i].postUrl = res.furl
      }
      // console.log(this.teamData)
    },
    fileRemove (i, res, file, fileList) {
      this.teamData[i].post = ''
      this.teamData[i].postUrl = ''
      // console.log(this.teamData)
    },
    Save () {
      let data = this.unfilledData
      for (let i in data) {
        if (data[i].inputData[0].value || data[i].inputData[1].value || data[i].inputData[2].value || this.teamData[i].post) {
          this.teamData[i].name = data[i].inputData[0].value
          this.teamData[i].department = data[i].inputData[1].value
          this.teamData[i].responsibility = data[i].inputData[2].value
        }
      }
      // 保存的是小写 checkAgentDutyTeam
      this.surveyRatingData.checkAgentDutyTeam = JSON.stringify(this.teamData)
      localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
      // console.log(this.teamData, this.surveyRatingData.checkAgentDutyTeam)
      this.$message.closeAll()
      this.$message({
        message: '草稿保存成功',
        type: 'success',
        // duration: 0,
        center: true
      })
      return true
    }
  },
  mounted () {
    var tempStatId = localStorage.getItem('statusId') ? localStorage.getItem('statusId') : '00'
    localStorage.setItem('manageStsa' + tempStatId, '1')
  }
}
</script>

<style scoped>
.management-team {
}
.management-team .management-team-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
  margin-bottom: 90px;
}
.management-team .management-team-main .unfilled-item {
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
}
.management-team .management-team-main .unfilled-item .operat {
  display: flex;
  justify-content: flex-end;
  box-sizing: border-box;
  padding-right: 30px;
}
.management-team .management-team-main .unfilled-item .operat i {
  font-size: 32px;
  color: #606266;
  cursor: pointer;
}
.management-team .management-team-main .unfilled-item .operat i:hover {
  color: #5cb6ff;
}
.management-team .management-team-main .input-component {
  justify-content: space-between;
  margin-bottom: 30px;
}
.management-team .management-team-main .file-upload {
}
</style>



