<template>
  <div class='step-bar'>
    <div class="step-head">
      <div class="step-head-item" v-for="(item, i) in stepArr" v-if="item.status == 'complete'" :class="item.status" :key="i">
        <div class="step-line"></div>
        <div class="step-icon"></div>
      </div>
      <span class="step-head-item" v-else :class="item.status" :key="i">
        <div class="step-line"></div>
        <div class="step-icon"></div>
      </span>
    </div>
    <div class="step-main">
      <div class="step-main-item" v-for="(item, i) in stepArr" :class="item.status" :key="i">
        <div class="step-text">
          <h4>{{item.title}}</h4>
          <p>{{item.text}}</p>
        </div>
        <div class="step-operat" @click="FillIn(item.path)">{{item.operat}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'StepBar',
  props: ['stepArr', 'FillIn'],
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.step-bar {
  display: flex;
}

.step-bar .step-head {
  width: 40px;
  box-sizing: border-box;
  padding: 15px 15px 0 5px;
}
.step-bar .step-head .step-head-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.step-bar .step-head .step-head-item .step-line {
  width: 1px;
  height: 48px;
  background: #ddd;
  margin: 6px 0;
}
.step-bar .step-head .step-head-item:first-child .step-line {
  display: none !important;
}
.step-bar .step-head .step-head-item .step-icon {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: rgba(0, 194, 80, 0);
}
.step-bar .step-head .step-head-item .step-icon:before {
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #ddd;
}
.step-bar .step-head span.step-head-item:nth-of-type(1) .step-icon {
  background: rgba(0, 194, 80, 0.3) !important;
}
.step-bar .step-head span.step-head-item:nth-of-type(1) .step-icon:before {
  background: limegreen;
}
.step-bar .step-head .step-head-item.complete .step-icon {
  background: url(data:image/png;base64,)
    no-repeat 0 0/100% 100% !important;
}
.step-bar .step-head .step-head-item.complete .step-icon:before {
  content: none;
}

.step-bar .step-head {
}
.step-bar .step-main {
  width: calc(100% - 40px);
  border-top: 1px solid #ddd;
}
.step-bar .step-main .step-main-item {
  height: 80px;
  box-sizing: border-box;
  border-bottom: 1px solid #ddd;
  display: flex;
}
.step-bar .step-main .step-main-item .step-text {
  flex: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.step-bar .step-main .step-main-item .step-text h4 {
  font-weight: bold;
  color: #333;
}
.step-bar .step-main .step-main-item .step-text p {
  color: #999;
}
.step-bar .step-main .step-main-item .step-operat {
  display: flex;
  align-items: center;
  color: cornflowerblue;
  cursor: pointer;
}
.step-bar .step-main .step-main-item.complete .step-operat {
  color: limegreen;
}
.step-bar .step-main .step-main-item.turndown .step-operat {
  color: red;
}
.step-bar .step-main .step-main-item .step-operat:after {
  content: '';
  display: block;
  width: 8px;
  height: 8px;
  border-top: 1px solid #aaa;
  border-right: 1px solid #aaa;
  transform: rotate(45deg);
  margin: 0 30px 0 15px;
}
</style>



